<template>
    <div id="triangle3">
    </div>
</template>

<script>
    import { Triangle3 } from '@/components/configs/duvalst3.js'
    export default {
        name : 'DuvalsTriangleThree',
        props : ['data'],
        data() {
            return {
                duvals : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.t3;
                return d;
            });

            this.duvals = new Triangle3('triangle3');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.t3;
                    return d;
                });
                
                this.duvals.updateData(data);
            }
        }
    }
</script>