<template>
    <v-dialog v-model="show" max-width="650" @click:outside="closePopup()">
        <template v-slot:default="dialog">
            <v-card>
                <v-toolbar dense color="red darken-3" dark><v-toolbar-item>Interpretations</v-toolbar-item></v-toolbar>
                <v-tabs color="red" centered v-model="tab">
                    <v-tab key="duvals">
                        Duvals Interpretation
                    </v-tab>
                    <v-tab key="others">
                        Other Interpretations
                    </v-tab>
                </v-tabs>
                <v-tabs-items v-model="tab">
                    <v-tab-item key="duvals">
                        <v-simple-table dense>
                            <template v-slot:default>
                                <tbody>
                                    <tr>
                                        <td>Triangle 1</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t1 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(0, details.t1) }}
                                            </v-chip>
                                        </td>
                                       
                                    </tr>
                                    <tr>
                                        <td>Triangle 2</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t2 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(1, details.t2) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Triangle 3 (Biotemp)</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t3_biotemp == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(2, details.t3) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Triangle 4</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t4 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(3, details.t4) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Triangle 5</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t5 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(4, details.t5) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Triangle 6</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t6 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(5, details.t6) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Triangle 7</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.t7 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(6, details.t7) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Pentagon 1</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.p1 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(7, details.p1) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Pentagon 2</td>
                                        <td>
                                            <v-chip small class="white--text"
                                                :color="details.p2 == 'Normal' ? 'green' : 'red'">
                                                {{ getFaultDescription(8, details.p2) }}
                                            </v-chip>
                                        </td>
                                    </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-tab-item>
                    <v-tab-item key="others" class="text-center">
                        <v-container>
                            <v-simple-table dense>
                                <template v-slot:default>
                                    <thead>
                                        <tr>
                                            <th class="text-left">Type</th>
                                            <th class="text-left">Interpretation</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td class="text-left">IEC Ratio</td>
                                            <td class="text-left">{{ details.iec_ratio }}</td>
                                        </tr>
                                        <tr>
                                            <td class="text-left">Dornenburg</td>
                                            <td class="text-left">{{ details.dornenberg_ratio }}</td>
                                        </tr>
                                        <tr>
                                            <td class="text-left">Roger's Ratio</td>
                                            <td class="text-left">{{ details.rogers_ratio }}</td>
                                        </tr>
                                        <tr>
                                            <td class="text-left">NEI Ratio</td>
                                            <td class="text-left">{{ details.nei }}</td>
                                        </tr>
                                        <tr>
                                            <td class="text-left">CO2/CO Ratio</td>
                                            <td class="text-left">{{ details.carbon_ratio }}</td>
                                        </tr>
                                    </tbody>
                                </template>
                            </v-simple-table>

                        </v-container>
                        <!-- <v-simple-table dense>
                            <template v-slot:default>
                                <tbody>
                                    <tr>
                                        <td>IEC Ratio</td>
                                        <td>{{ details.iec_ratio }}</td>
                                    </tr>
                                    <tr>
                                        <td>Dornenberg's Ratio</td>
                                        <td>{{ details.dornenberg }}</td>
                                    </tr>
                                    <tr>
                                        <td>Roger's Ratio</td>
                                        <td>{{ details.rogers_ratio }}</td>
                                    </tr>
                                    <tr>
                                        <td>NEI</td>
                                        <td>{{ details.nei }}</td>
                                    </tr>
                                </tbody>
                            </template>
                        </v-simple-table> -->
                    </v-tab-item>
                </v-tabs-items>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn depressed text elevation="3" small @click="addComents(details)" >Add Comment</v-btn>
                    <v-btn depressed  color="error" text elevation="3" small @click="closePopup()">Close</v-btn>
                </v-card-actions>
            </v-card>
        </template>
    </v-dialog>
</template>
<style scoped>
.text-sm {
    font-size: 12px;
    margin-bottom: 0px;
}
</style>
<script>
import { symbolMixin } from '@/mixins/symbolMixin.js';
export default {
    name: 'InterpretationsDetails',
    props: ['show', 'details'],
    mixins: [symbolMixin],
    data() {
        return {
            tab: 'duvals',
            validation_dialog: true
        }
    },
    methods: {
        closePopup() {
            this.$emit('close');
        },
        getFaultDescription(idx, interpretation) {
            if (interpretation != 'Normal') {
                console.log(idx, interpretation);
                return interpretation + ' : ' + this.symbols[idx].legends[interpretation]['description'];
            }
            return 'Normal';
        },
        addComents(details){
            this.$emit('close');
            this.$emit('addcomment', details);
        }   
    }
}
</script>