<template>
    <div id="triangle4">
    </div>
</template>

<script>
    import { Triangle4 } from '@/components/configs/duvalst4.js'
    export default {
        name : 'DuvalsTriangleFour',
        props : ['data'],
        data() {
            return {
                duvalst4 : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.t4;
                return d;
            });

            this.duvals = new Triangle4('triangle4');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.t4;
                    return d;
                });

                this.duvals.updateData(data);
            }
        }
    }
</script>