<template>
      <v-row
        align="center"
        justify="center"
      >
        <v-col
          class="text-center"
          cols="12" sm="8"
        >
        
        <v-alert class="mx-auto pointer darken-3 pa-5" dense outlined text color="green">
            
          <h1 class="text-h4 font-weight-thin mb-2">
            THANK YOU!
          </h1>
          <p class="text-h5 font-weight-thin mb-3">You have successfully subscribed to C8 Vortex.</p>
          <h3 class="subheading " >
            Your <span class="text-bold text-uppercase" >{{ profile.subscription.subscription_type }}</span> subscription
                    will expire on {{ dateFormat(profile.subscription.expiration_date) }}.
          </h3>
          <h5>You will be redirected to Dashboard in {{ counter }}</h5>
        </v-alert>
        </v-col>
      </v-row>
  </template>
<style scoped>

</style>
<script>
    import axios from 'axios';
    export default {
        name : 'ThankYOu',
        data() {
            return {
                profile : {
                    company : {},
                    subscription : {}
                },
                counter : 4
            }
        },
        mounted() {
            if(localStorage.getItem('user')) {
                axios({
                    url : process.env.VUE_APP_BASEURL + '/profile',
                    method : 'GET',
                    headers : {
                        'Content-Type' : 'application/x-www-form-urlencoded',
                        'Authorization' : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                    }
                }).then(res => {
                    this.profile = res.data.user;
                    localStorage.setItem('user', JSON.stringify(res.data));

                    setInterval(count => {
                        this.counter -= 1;
                        if(this.counter == 0) {
                            this.$router.push({name : 'Home'});
                        }
                    }, 1000);

                }).catch(err => {
                    this.profile = null;
                })
            }
        },
        methods : {
            dateFormat(date_str) {
                var date = new Date(date_str);

                return date.toDateString();
            }
        }
    }
</script>