<template>
    <div>
        <div id="triangle1">
        </div>
    </div>
</template>

<script>
    import { Triangle1 } from '@/components/configs/duvalst1.js'
    export default {
        name : 'DuvalsTriangleOne',
        props : ['data'],
        data() {
            return {
                duvalst1 : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.t1;
                return d;
            });

            this.duvals = new Triangle1('triangle1');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.t1;
                    
                    return d;
                });

                this.duvals.updateData(data);
            }
        }
    }
</script>