<template>
    <div id="triangle2">
    </div>
</template>

<script>
    import { Triangle2 } from '@/components/configs/duvalst2.js'
    export default {
        name : 'DuvalsTriangleTwo',
        props : ['data'],
        data() {
            return {
                duvals : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.t2;
                return d;
            });

            this.duvals = new Triangle2('triangle2');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.t2;
                    return d;
                });
                
                this.duvals.updateData(data);
            }
        }
    }
</script>