<template>
    <div>
        <v-dialog v-model="dialog" width="500" :persistent="persistent">
            <v-card>
                <v-progress-linear v-if="loading" indeterminate color="red darken-2"></v-progress-linear>
                <v-card-title>Update Profile</v-card-title>
                <v-card-subtitle>Your profile needs to be updated before you can proceed.</v-card-subtitle>
                <v-card-text>
                    <v-text-field color="red darken-2" v-model="user.name" readonly label="Name" dense outlined></v-text-field>
                    <v-text-field color="red darken-2" v-model="user.email" readonly label="Company Email" dense outlined></v-text-field>
                    <v-text-field color="red darken-2" v-model="user.company.name" label="Company" dense outlined></v-text-field>
                    <v-text-field color="red darken-2" v-model="user.company.domain" readonly label="Domain" dense outlined></v-text-field>
                    <!-- <v-text-field v-model="user.company.state" label="Country Code" dense outlined></v-text-field> -->
                    <div class="form-group">
                        <v-select dense color="red darken-2"
                        v-model="selectedCountry"
                        :items="countryOptions"
                        item-value="code"
                        item-text="name"
                        label="Country"
                        outlined
                        clearable
                        @input="onCountrySelected"
                        >
                        <template v-slot:selection="{ item }">
                            {{ item.name }} ({{ item.code }})
                        </template>
                        </v-select>
                    </div>
                    <v-combobox color="red darken-2" v-model="user.company.industry" :items="industries" label="Industry" Clearable dense
                        outlined></v-combobox>
                    <!-- <v-select v-model="select" :hint="`${select.state}, ${select.abbr}`" :items="items" item-text="state"
                    item-value="abbr" label="Select" persistent-hint return-object dense outlined></v-select> -->
                </v-card-text>
                <v-card-actions>
                    <v-btn block class="red darken-4 white--text" :disabled="loading"
                        @click="updateProfile()">
                        Update Profile
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="validation_dialog" max-width="300">
            <template>
                <v-card>
                    <v-card-text>
                        <div
                            style="white-space: pre; display:grid; place-items:center; padding-top:2em; margin-left:1em; margin-right:1em;">
                            {{ dialogMessage }}</div>
                    </v-card-text>
                    <v-card-actions class="justify-end">
                        <v-btn dark small color="red" text @click="validation_dialog = false">OK</v-btn>
                    </v-card-actions>
               
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<style>
.form-group {
  margin-bottom: 1rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.select-wrapper {
  position: relative;
}

select {
  display: block;
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  background-color: transparent;
  font-size: 1rem;
  line-height: 1.5;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.select-arrow {
  position: absolute;
  top: 50%;
  right: 1.1rem;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0.375rem 0.375rem 0 0.375rem;
  border-color: #ccc transparent transparent transparent;
  pointer-events: none;
}
</style>

<script>
import countryDataList from 'country-list';

export default
    {
        name: 'CompanyDetailsDialog',
        props: ['details', 'dialog'],
        data() {
            return {
                loading: false,
                validation_dialog: false,
                countryOptions: countryDataList.getData(),
                selectedCountry: null,
                dialogMessage: '',
                dialogTitle: '',
                user: {
                    company: {}
                },
                persistent: true,
                industries: [
                    'Air Transfortaion',
                    'Apparel Manufacturing',
                    'Beverage and Tobacco Product Manufacturing',
                    'Chemical Manufacturing',
                    'Crop Productions',
                    'Fabricated Metal Product Manufacturing',
                    'Gasoline Station',
                    'Machinery Manufacturing',
                    'Mining, Quarrying, and Oil and Gas Extraction',
                    'Natural Resources and Mining',
                    'Oil and Gas Extraction',
                    'Plastics and Rubber Products Manufacturing',
                    'Primary Metal Manufacturing',
                    'Professional and Business Services',
                    'Professional, Scientific, and Technical Services',
                    'Publishing Industries',
                    'Rail Transportation',
                    'Repair and Maintenance',
                    'Speciality Trade Contractors',
                    'Telecommunications',
                    'Warehousing and Storage',
                    'Water Transportation',
                    'Wood Product Manufacturing',
                ],
            }

        },
        // created() {
        //     const countries = countryDataList.getNames();
        //     const countryCodes = countryDataList.getCodes();

        //     for (let i = 0; i < countries.length; i++) {
        //     this.countryOptions.push({ name: countries[i], code: countryCodes[i] });
        //     }
        // },
                watch: {
            'details'() {
                this.user = this.details;
            },

        },
        methods: {
            onCountrySelected() {
                this.user.company.country_code = this.selectedCountry;
                console.log(this.user.company.country_code);
            },
            updateProfile() {
                if (!this.user.name) {
                    this.validation_dialog = true;
                    this.dialogMessage = 'Name field is required.';
                    return;
                }
                if (!this.user.email) {
                    this.validation_dialog = true;
                    this.dialogMessage = 'Email field is required.';
                    return;
                }
                if (!this.user.company.name) {
                    this.validation_dialog = true;
                    this.dialogTitle = 'Warning';
                    this.dialogMessage = 'Company field is required.';
                    return;
                }
                if (!this.user.company.domain) {
                    this.validation_dialog = true;
                    this.dialogMessage = 'Domain field is required.';
                    return;
                }
                if (!this.user.company.country_code) {
                    this.validation_dialog = true;
                    this.dialogMessage = 'Select country.';
                    return;
                }
                if (!this.user.company.industry) {
                    this.validation_dialog = true;
                    this.dialogMessage = 'Select industry.';
                    return;
                }

                this.loading = true;
                this.$emit('update', this.user)
            }
        }

    }
</script>