import * as d3 from 'd3'
export class Duvals {
	constructor(container) {
		this.container = container;
		this.config = {
			width : '438.8',
			height : '380'
		};
		this.d3 = d3;
	}

	getD3() {
		return this.d3;
	}

    getContainer() {
        return this.container;
    }

    getConfig() {
        return this.config;
    }

    getHexColor(idx, total) { //get the color assigned to specific plot from yellow(oldest) to red(newest)
	    var r = 255;
	    var g = 0;
	    var b = 0;

	    //yellow to red (255, 255, 0) - (255, 0, 0)
	    var interval = 255 / total;
	    var g = 255 - Math.floor(((idx + 1 ) * interval));
	    var r_hex = Number(r).toString(16).length < 2 ? "0" + Number(r).toString(16) : Number(r).toString(16);
	    var g_hex = Number(g).toString(16).length < 2 ? "0" + Number(g).toString(16) : Number(g).toString(16);
	    var b_hex = Number(b).toString(16).length < 2 ? "0" + Number(b).toString(16) : Number(b).toString(16);

	    var hex_color = r_hex + g_hex + b_hex;
	    return "#"+ hex_color;
	}
}