<template>
    <div >
        <v-row dense>
            <v-col cols="12" sm="6" md="4" class="full-height pr-0 pb-0 tabscrollbar">
                <v-card class="column-header" dense block dark color="red darken-4">Assets Hierarchy</v-card>

                <v-btn text small block color="red" class="darken-2" @click="addRootElement()">
                    <v-icon>mdi-plus</v-icon> New Site Element
                </v-btn>

                <v-divider></v-divider>
                <v-treeview v-model="tree" color="red" :items="items" activatable item-key="id"
                    @update:active="getDetails" dense>
                    <template v-slot:prepend="{ item }">
                        <v-icon>
                            mdi-cube
                        </v-icon>
                    </template>
                </v-treeview>
            </v-col>
            <v-divider vertical></v-divider>
            <v-col cols="12" sm="6" md="8" class="full-height pl-1 pb-0">
                <v-progress-linear v-if="loading" indeterminate color="red lighten-2"></v-progress-linear>
                <v-tabs background-color="transparent" color="red" v-model="tab" left>
                    <v-tabs-slider color="red"></v-tabs-slider>

                    <v-tab key="general" v-if="items.length > 0">
                        Details
                    </v-tab>
                    <v-tab key="Status" v-if="elements == 1">
                        Transformer Status
                    </v-tab>
                    <v-tab key="attributes" v-if="elements == 2">
                        Attributes
                    </v-tab>
                    <v-tab key="data" v-if="transformer_details.length > 0">
                        Data Entry
                    </v-tab>
                </v-tabs>
                <v-divider></v-divider>
                <v-tabs-items class="tabscrollbar" v-model="tab">
                    <v-tab-item v-if="items.length > 0" key="general">
                        <v-container>
                            <div class="my-2">
                                <v-text-field label="Name" outlined dense hide-details v-model="element.name"
                                    color="red"></v-text-field>
                            </div>
                            <div class="my-2">
                                <v-textarea label="Description" outlined dense hide-details
                                    v-model="element.description" color="red"></v-textarea>
                            </div>
                            <div class="my-2">
                                <v-text-field label="Path" outlined dense hide-details v-model="element.path"
                                    color="red" disabled></v-text-field>
                            </div>
                            <div class="text-right">
                                <v-btn text small color="red" @click="updateElement()">
                                    <v-icon>mdi-content-save</v-icon> Update
                                </v-btn>
                            </div>
                        </v-container>
                        <v-divider></v-divider>
                        <div style="margin-top:5px;" class="text-right">
                           
                                    <v-btn text small color="red" @click="removeChild()">
                                        <v-icon>mdi-minus</v-icon> Delete Element
                                    </v-btn>
                        </div>
                        <div style="margin-top:-25px;" v-if="elements == 0">
                          
                                    <v-btn text small color="red" @click="addSubstation()">
                                        <v-icon>mdi-plus</v-icon> New Child Element
                                    </v-btn>
                        </div>
                        <div style="margin-top:-25px;" v-if="elements == 1">
                                    <v-btn text small color="red" @click="addTransformer()">
                                        <v-icon>mdi-plus</v-icon> New Child Element
                                    </v-btn>
                        </div>
                    
                    </v-tab-item>
                    <v-tab-item v-if="elements == 1" key="Status">
                        <div class="pa-3">
                            <v-row dense>
                                <v-col cols="12" md="4" v-for="t in transformers" :key="t.id">
                                    <v-alert class="mx-auto pointer" dense outlined text height="220"
                                       @click="goToTransformer(t)" :color="t.status.normal ? 'green' : 'red'">
                                    <v-icon v-if="t.status.normal" color="green" left>
                                        mdi-check-circle-outline
                                    </v-icon>
                                    <v-icon v-if="!t.status.normal" color="red" left>
                                        mdi-car-tire-alert
                                    </v-icon>
                                        <v-card-title class="shrink-text text--primary">{{ t.name }}</v-card-title>

                                        <v-card-subtitle  >
                                            {{ t.path.replaceAll('\\', '>') }}
                                        </v-card-subtitle>

                                        <v-card-text class="text--primary" >
                                            <div v-if="t.status.normal">{{ !t.latest_value ? 'No Data' : 'Transformer is in Good Condition.' }}</div>
                                            <div v-if="!t.status.normal">{{ !t.latest_value ? 'No Data' : 'Transformer reached the Upper Limit.' }}</div>
                                        </v-card-text>

                                    </v-alert>
                                </v-col>
                            </v-row>
                        </div>
                    </v-tab-item>
                    <v-tab-item v-if="elements == 2" key="attributes">
                        <v-container v-if="attributes.length == 0">
                            <v-btn block @click="createAttributes(element.id)">
                                <v-icon>mdi-triangle-outline</v-icon>
                                Create DGA Attributes
                            </v-btn>
                        </v-container>
                        <v-container  style="position:relative; bottom:5px;">
                            <v-btn v-if="transformer_details.length == 0" block @click="transformer_dialog = true;">
                                <v-icon>mdi-triangle-outline</v-icon>
                                Create Transformer Details
                            </v-btn>
                        </v-container>
                        <v-container style="position:relative; bottom:15px;" v-if="transformer_details.length > 0">
                            <v-card class="pa-2" style="background-color:transparent;">
                                <v-card-title class="pt-0">Tranformer Details</v-card-title>
                                    <v-row>
                                        <v-col cols="12" md="4">
                                            <v-text-field color="red" readonly v-model="transformer_details[0].manufacturer"
                                                label="Manufacturer"  outlined dense hide-details></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="4">
                                            <v-text-field color="red" readonly v-model="transformer_details[0].type"
                                                label="Type" outlined dense hide-details></v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="4">
                                            <v-text-field color="red" v-model="transformer_details[0].startup_date"
                                                label="Commisioning Date"  readonly hide-details
                                                outlined dense ></v-text-field>
                                        </v-col>
                                    </v-row>

                                        <v-row>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly
                                                    v-model="transformer_details[0].construction_year"
                                                    label="Construction Year" outlined dense
                                                    hide-details></v-text-field>
                                            </v-col>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly v-model="transformer_details[0].age_band"
                                                    label="Age | Age Band" outlined dense hide-details></v-text-field>
                                            </v-col>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly v-model="transformer_details[0].line_capacity"
                                                    label="Line Capacity" outlined dense hide-details></v-text-field>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly
                                                    v-model="transformer_details[0].winding_voltage"
                                                    label="Winding Voltage" outlined dense hide-details></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="4">
                                                <v-textarea color="red" readonly v-model="transformer_details[0].asset_desc"
                                                    name="input-7-1" label="Asset Description" value="" rows="1"
                                                    outlined dense hide-details>
                                                </v-textarea>
                                            </v-col>
                                            <v-col cols="12" sm="4">
                                                <v-textarea color="red" readonly v-model="transformer_details[0].address"
                                                    label="Address" value="" rows="1" outlined dense hide-details>
                                                </v-textarea>
                                            </v-col>
                                        </v-row>
                                        <v-row>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly
                                                    v-model="transformer_details[0].country_manufacturer"
                                                    label="Country Manufacturer" outlined dense
                                                    hide-details></v-text-field>
                                            </v-col>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly v-model="transformer_details[0].serial_no"
                                                    label="Serial No." outlined dense hide-details></v-text-field>
                                            </v-col>
                                            <v-col cols="12" md="4">
                                                <v-text-field color="red" readonly v-model="transformer_details[0].model_no"
                                                    label="Model No." outlined dense hide-details></v-text-field>
                                            </v-col>
                                        </v-row>
            
                           </v-card>
                        </v-container>
                        <v-container style="position:relative; bottom:25px;">
                            <v-card style="background-color:transparent;">
                                <v-card-title>DGA Attributes</v-card-title>
                                    <v-divider></v-divider>
                                    <v-simple-table   style="background-color:transparent;" class="scrollbar" dense>
                                        <template v-slot:default>
                                            <thead>
                                                <tr>
                                                    <th class="text-left">
                                                        Parameter Name
                                                    </th>
                                                    <th class="text-left">
                                                        Description
                                                    </th>
                                                    <th class="text-left">
                                                        Stream Name
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="item in attributes" :key="item.id">
                                                    <td>{{ item.name }}</td>
                                                    <td>{{ item.description }}</td>
                                                    <td>{{ item.stream_name }}</td>

                                                </tr>
                                            </tbody>
                                        </template>
                                    </v-simple-table>
                                </v-card>
                        </v-container>
                    </v-tab-item>
                    <v-tab-item v-if="transformer_details.length > 0" key="data">
                        <v-container>
                            <v-row>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" v-model="attribute_value.date" label="Date"
                                        append-icon="mdi-calendar" readonly hide-details outlined dense
                                        @click:append="datepicker = true"></v-text-field>
                                </v-col>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Time" value="12:30:00"
                                        v-model="attribute_value.time"  @click:append="timepicker = true"  append-icon="mdi-clock-edit-outline" readonly hide-details dense outlined>
                                    </v-text-field>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Acetylene (C2H2)" dense outlined hide-details
                                        v-model="attribute_value.acetylene" type="number"></v-text-field>
                                </v-col>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Ethylene (C2H4)" dense outlined hide-details
                                        v-model="attribute_value.ethylene"></v-text-field>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Methane (CH4)" dense outlined hide-details
                                        v-model="attribute_value.methane"></v-text-field>
                                </v-col>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Ethane (C2H6)" dense outlined hide-details
                                        v-model="attribute_value.ethane"></v-text-field>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Hydrogen (H)" dense outlined hide-details
                                        v-model="attribute_value.hydrogen"></v-text-field>
                                </v-col>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Oxygen (O2)" dense outlined hide-details
                                        v-model="attribute_value.oxygen"></v-text-field>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Carbon Monoxide (CO)" dense outlined hide-details
                                        v-model="attribute_value.carbon_monoxide"></v-text-field>
                                </v-col>
                                <v-col cols="12" md="6">
                                    <v-text-field color="red" label="Carbon Dioxide (CO2)" dense outlined hide-details
                                        v-model="attribute_value.carbon_dioxide"></v-text-field>
                                </v-col>
                            </v-row>
                            <!-- <v-row>
                                <v-col cols="12" md="6">
                                    <v-text-field label="TDCG" dense outlined hide-details></v-text-field>
                                </v-col>
                            </v-row> -->
                            <v-row>
                                <v-col cols="12" md="12">
                                    <v-btn block class="red darken-4 white--text" :disabled="loading"
                                        @click="writeData()">
                                        Submit Data
                                    </v-btn>
                                </v-col>
                            </v-row>

                            <div class="pt-3">
                                <gasses-data :data="attribute_values" @showDetails="showLogDetails"></gasses-data>
                            </div>
                        </v-container>
                    </v-tab-item>
                </v-tabs-items>
  
            </v-col>
        </v-row>
        <v-dialog v-model="element_dialog" max-width="340" persistent>

            <v-card>
                <v-toolbar v-if="elements == 0" class="red darken-3" dense dark>
                    Substation Details
                </v-toolbar>
                <v-toolbar v-if="elements == 1" class="red darken-3" dense dark>
                    Transformer Details
                </v-toolbar>
                <v-divider></v-divider>
                <v-progress-linear v-if="save_loading" indeterminate color="red lighten-2"></v-progress-linear>
                <v-card-text>
                    <div class="my-2">
                        <v-text-field color="red" label="Name" hide-details v-model="new_element.name"></v-text-field>
                    </div>
                    <div class="my-2">
                        <v-textarea color="red" no-resize label="Description" hide-details rows="3"
                            v-model="new_element.description"></v-textarea>
                    </div>
                    <div class="my-2">
                        <v-checkbox v-if="elements == 1" color="red" label="Create DGA Attribute Templates"
                           v-model="new_element.template_created"></v-checkbox>
                    </div>
                </v-card-text>
                <v-card-actions>
                    <v-btn dark small color="red" text @click="element_dialog = false">
                        <v-icon>mdi-arrow-left</v-icon> Cancel
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-btn small color="indigo" dark text @click="saveElement()">
                        <v-icon>mdi-content-save</v-icon> Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="rootElement_dialog" max-width="340" persistent>

            <v-card>
                <v-toolbar class="red darken-3" dense dark>
                    Site Details
                </v-toolbar>
                <v-divider></v-divider>
                <v-progress-linear v-if="save_loading" indeterminate color="red lighten-2"></v-progress-linear>
                <v-card-text>
                    <div class="my-2">
                        <v-text-field color="red" label="Name" hide-details v-model="new_element.name"></v-text-field>
                    </div>
                    <div class="my-2">
                        <v-textarea color="red" no-resize label="Description" hide-details rows="3"
                            v-model="new_element.description"></v-textarea>
                    </div>
                </v-card-text>
                <v-card-actions>
                    <v-btn dark small color="red" text @click="rootElement_dialog = false">
                        <v-icon>mdi-arrow-left</v-icon> Cancel
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-btn small color="indigo" dark text @click="saverootElement()">
                        <v-icon>mdi-content-save</v-icon> Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="transformer_dialog" max-width="800">
            <v-card>
                <v-progress-linear v-if="save_loading" indeterminate color="red lighten-2"></v-progress-linear>
                <v-card-title>Tranformer Details</v-card-title>
                <v-divider></v-divider>

                <v-card-text class="pt-2">
                    <v-row>
                        <v-col cols="12" md="4">
                            <v-text-field color="red" v-model="transformer.manufacturer" label="Manufacturer" outlined
                                dense hide-details></v-text-field>
                        </v-col>
                        <v-col cols="12" md="4">
                            <v-text-field color="red" v-model="transformer.type" label="Type" outlined dense
                                hide-details></v-text-field>
                        </v-col>
                        <v-col cols="12" md="4">
                            <v-text-field color="red" v-model="date" label="Commisioning Date" append-icon="mdi-calendar"
                                hide-details readonly outlined dense
                                @click:append="startdatepicker = true"></v-text-field>
                        </v-col>
                    </v-row>
                        <v-row>
                            <v-col cols="12" sm="4">
                                <v-select v-model="transformer.construction_year" :items="reversedYearsList" outlined
                                    dense hide-details color="red"  label="Construction Year">
                                    </v-select>
                            </v-col>
                            <v-col cols="12" md="4">
                                <v-text-field color="red" readonly v-model="computedAge" label="Age | Age Band" outlined
                                    dense hide-details></v-text-field>
                            </v-col>
                            <v-col cols="12" md="4">
                                <v-text-field color="red" v-model="transformer.line_capacity" label="Line Capacity"
                                    outlined dense hide-details></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col cols="12" md="4">
                                <v-text-field color="red" v-model="transformer.winding_voltage" label="Winding Voltage"
                                    outlined dense hide-details></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="4">
                                <v-textarea color="red" v-model="transformer.asset_desc" name="input-7-1"
                                    label="Asset Description" value="" rows="1" outlined dense hide-details>
                                </v-textarea>
                            </v-col>
                            <v-col cols="12" sm="4">
                                <v-textarea color="red" v-model="transformer.address" label="Address" value="" rows="1"
                                    outlined dense hide-details>
                                </v-textarea>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col cols="12" md="4">
                                <v-select dense color="red darken-2"
                                v-model="selectedCountry"
                                :items="countryOptions"
                                item-value="name"
                                item-text="name"
                                label="Country Manufacturer"
                                outlined
                                clearable
                                @input="onCountrySelected"
                                >
                                <template v-slot:selection="{ item }">
                                    {{ item.name }} ({{ item.code }})
                                </template>
                                </v-select>
                            </v-col>
                            <v-col cols="12" md="4">
                                <v-text-field color="red" v-model="transformer.serial_no" label="Serial No." outlined
                                    dense hide-details></v-text-field>
                            </v-col>
                            <v-col cols="12" md="4">
                                <v-text-field color="red" v-model="transformer.model_no" label="Model No." outlined
                                    dense hide-details></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col cols="12" md="12">
                                <v-btn block class="red darken-4 white--text" :disabled="save_loading"
                                    @click="saveDetails()">
                                    Save Details
                                </v-btn>
                            </v-col>
                        </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
        <v-dialog v-model="validation_dialog" max-width="300">
            <template>
                <v-card>
                    <v-card-text>
                        <div
                            style="white-space: pre; display:grid; place-items:center; padding-top:2em; margin-left:1em; margin-right:1em;">
                            {{ dialogMessage }}</div>
                    </v-card-text>
                    <v-card-actions class="justify-end">
                        <v-btn dark small color="red" text @click="validation_dialog = false">OK</v-btn>
                    </v-card-actions>

                </v-card>
            </template>
        </v-dialog>

        <v-dialog v-model="confirm_dialog" max-width="300">
            <v-card>
                <v-card-title>
                </v-card-title>
                <v-card-text>
                    Are you sure you want to delete {{ element.path }}?
                    <br>
                    <div>Note : This action cannot be undone.</div>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn text @click="confirm_dialog = false">
                        No
                    </v-btn>

                    <v-btn color="red" text @click="removeChildProcess()">
                        Yes
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <log-details :details="attribute_value_details" :show="showLogPopup" @close="hideLogPopup"></log-details>
        <date-picker :dialog="datepicker" :date="attribute_value.date" @setDate="getDate"
            @close="closeDatePicker"></date-picker>
        <date-picker :dialog="startdatepicker" :date="transformer.startup_date" @setDate="startDate"
            @close="closeDatePicker"></date-picker>
        <time-picker :dialog="timepicker" @setTime="setTime" :time="attribute_value.time" @close="closeTimePicker" :current_time="time"></time-picker>
    </div>
</template>
<style scoped>
.full-height {
    height: calc(100vh - 53px);
}

.column-header {
    padding: 10px;
    text-align: center;
    border-radius: 0;
}

.border {
    border: 1px solid gray;
}

.scrollbar {
    height: 250px;
    overflow-y: scroll;
    /* Add the ability to scroll */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.scrollbar::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.scrollbar {
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}
.tabscrollbar {
    background-color: transparent;
    height: 650px;
    overflow-y: scroll;
    /* Add the ability to scroll */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.tabscrollbar::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.tabscrollbar {
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}
.pointer {
  cursor: pointer;
}
.shrink-text {
   overflow: hidden;
   white-space: nowrap;
   text-overflow: ellipsis;
   width: 100%;
}
</style>
<script>
import Chart from '@/components/Chart.vue';
import GassesData from '@/components/tables/GassesData.vue';
import LogDetails from '@/components/popups/LogDetails.vue';
import DatePicker from '@/components/pickers/DatePicker.vue';
import countryDataList from 'country-list';
import TimePicker from '@/components/pickers/TimePicker.vue';
import axios from 'axios';
export default {
    name: 'Assets',
    components: {
        Chart,
        DatePicker,
        GassesData,
        LogDetails,
        TimePicker,
    },
    data() {
        return {
            loading: false,
            save_loading: false,
            validation_dialog: false,
            dialogMessage: '',
            dialogTitle: '',
            initiallyOpen: [],
            tree: [],
            tab: 'general',
            element_dialog: false,
            element: {},
            yearsList:[], 
            new_element: {},
            items: [],
            attributes: [],
            showAssetDetails: true,
            confirm_dialog: false,
            attribute_value: {},
            transformer_details: [],
            attribute_values: [],
            transformer: {},
            elements: [],
            attribute_value_details: {},
            startdatepicker: false,
            datepicker: false,
            timepicker: false,
            showLogPopup: false,
            transformers: [],
            transformer_dialog: false,
            rootElement_dialog: false,
            date: null,
            time: null,
            checkBox:true,
            // asset: null,
            // assets: [],
            countryOptions: countryDataList.getData(),
            selectedCountry: null,

        }
    },
    mounted() {
        
        axios({
            url: process.env.VUE_APP_BASEURL + '/hierarchy',
            method: 'GET',
            headers: {
                'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
            }
        }).then(response => {
            this.items = response.data;
        }).catch(err => {
            if (err.response.status == 401) {
                this.$router.push({ name: 'Welcome' });
                alert("Unauthorized!");
            }
        });
    },
    computed: {
        computedAge() {
            this.startUp = new Date(this.date)
            this.currentDate = new Date()
            this.age = this.currentDate.getFullYear() - this.startUp.getFullYear()
            this.ageBand = ''
            if (this.age >= 0 && this.age < 18) {
                this.ageBand = "0-17"
            } else if (this.age >= 18 && this.age < 25) {
                this.ageBand = "18-24";
            } else if (this.age >= 25 && this.age < 35) {
                this.ageBand = "25-34";
            } else if (this.age >= 35 && this.age < 50) {
                this.ageBand = "35-49";
            } else {
                this.ageBand = "50+";
            }
            return `${this.age} Yr | ${this.ageBand} Yrs`
        },
        reversedYearsList() {
            this.yearsList = Array.from({length: new Date().getFullYear() - 1900 + 1}, (_, i) => 1900 + i);
            return this.yearsList.slice().reverse();
    }
       
    },
    watch: {

        'computedAge'() {
            this.transformer.age_band = this.computedAge;
        }
    },

    methods: {
        onCountrySelected() {
                this.transformer.country_manufacturer = this.selectedCountry;
                console.log(this.user.company.country_code);
            },
        getDetails(value) {
            if (value.length == 0) {
                this.element = {};
                this.attributes = [];
                // this.transformer_details = {};
                return;
            }
            this.loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + value[0],
                method: 'GET',
                headers: {
                    Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(response => {
                this.element = response.data.details;
                this.elements = response.data.details.path.split('\\').length - 1;
                this.attributes = response.data.attributes;
                this.attribute_values = response.data.attribute_values;
                this.attribute_value = {};
                this.transformers = response.data.transformers;
                this.transformer_details = response.data.transformer_details;
                console.log(this.element);
                this.loading = false;
            }).catch(err => {
                console.log(err);
                this.loading = false;
            })
        },
        addRootElement() {
            this.new_element = {
                id: null,
                company_id: JSON.parse(window.localStorage.getItem('user')).company_id
            };
            this.rootElement_dialog = true;
        },
        addSubstation() {
            this.new_element = {
                parent_id: this.element.id,
                company_id: JSON.parse(window.localStorage.getItem('user')).company_id
            };
            this.element_dialog = true;
        },
        addTransformer() {
            this.new_element = {
                parent_id: this.element.id,
                template_created: true,
                company_id: JSON.parse(window.localStorage.getItem('user')).company_id
            };
            this.element_dialog = true;
        },
        removeChild() {
            this.confirm_dialog = true;
        },
        removeChildProcess() {
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + this.element.id,
                method: 'DELETE',
                headers: {
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.items = res.data;
                this.transformers = {};
                this.confirm_dialog = false;
            }).catch(err => {

            });
        },
        updateElement() {
            this.loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + this.element.id,
                method: 'PUT',
                data: this.element,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.loading = false;
            }).catch(res => {
                this.loading = false;
            })
        },
        saveElement() {
            if (!this.new_element.name) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Element name field is required.';
                return;
            }

            if (this.elements == 1) {
                if (!this.new_element.template_created) {
                    this.validation_dialog = true;
                   
                    this.dialogMessage = 'Check Create DGA Attribute.';
                    return;
                }
            }

            this.save_loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + (this.element.id ? this.element.id : 0),
                method: 'POST',
                data: this.new_element,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.save_loading = false;
                this.items = res.data;
                console.log(this.items);
                this.element_dialog = false;
                this.rootElement_dialog = false;
            }).catch(err => {
                alert(err.response.data.message);
                this.save_loading = false;
            })
        },

        saverootElement() {
            if (!this.new_element.name) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Element name field is required.';
                return;
            }

            this.save_loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + (this.element.id ? this.element.id : 0),
                method: 'POST',
                data: this.new_element,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.save_loading = false;
                this.items = res.data;
                console.log(this.items);
                this.element_dialog = false;
                this.rootElement_dialog = false;
            }).catch(err => {
                alert(err.response.data.message);
                this.save_loading = false;
            })
        },


        createAttributes(id) {
            this.loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + id + '/attributes',
                method: 'POST',
                data: { element_id: id },
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.loading = false;
                this.attributes = res.data;
                console.log(this.attributes);
            }).catch(err => {
                this.loading = false;
            });
        },
        getDate(date) {
            this.attribute_value.date = date;
            this.datepicker = false;
        },
        setTime(time){
            this.attribute_value.time = time;
            this.timepicker = false;
        },
        startDate(date) {
            this.date = date
            this.transformer.startup_date = this.date;
            this.startdatepicker = false;
        },
        closeTimePicker(){
            this.timepicker = false;
        },
        closeDatePicker() {
            this.datepicker = false;
            this.startdatepicker = false;
        },
        writeData() {
            if (!this.attribute_value.date) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Select date.';
                return;
            }
            if (!this.attribute_value.time) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Select time.';
                return;
            }
            if (!this.attribute_value.acetylene) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Acetylene field is required.';
                return;
            }
            if (!this.attribute_value.ethylene) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Ethylene field is required.';
                return;
            }
            if (!this.attribute_value.methane) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Methane field is required.';
                return;
            }
            if (!this.attribute_value.ethane) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Ethane field is required.';
                return;
            }
            if (!this.attribute_value.hydrogen) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Hydrogen field is required.';
                return;
            }
            if (!this.attribute_value.oxygen) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Oxygen field is required.';
                return;
            }
            if (!this.attribute_value.carbon_monoxide) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Carbon Monoxide field is required.';
                return;
            }
            if (!this.attribute_value.carbon_dioxide) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Carbon Dioxide field is required.';
                return;
            }
            this.loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + this.element.id + '/attribute-values',
                method: 'POST',
                data: this.attribute_value,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.loading = false;
                this.attribute_values = res.data;
                this.attribute_value = {};
            }).catch(err => {
                console.log(err.response);
                if (err.response.status == 400) {
                    alert(err.response.data.error);
                }
                else {
                    alert("Something went wrong. Please Try again.");
                }
                this.loading = false;
            })
        },
        saveDetails() {
            if (!this.transformer.manufacturer) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Manufacturer.';
                return;
            }
            if (!this.transformer.type) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Transformer type.';
                return;
            }
            if (!this.transformer.startup_date) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please select Commisioning date.';
                return;
            }
            if (!this.transformer.construction_year) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Construction year.';
                return;
            }
            if (!this.transformer.line_capacity) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Line capacity.';
                return;
            }
            if (!this.transformer.winding_voltage) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Winding voltage.';
                return;
            }
            if (!this.transformer.asset_desc) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Asset description.';
                return;
            }
            if (!this.transformer.address) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Address.';
                return;
            }
            if (!this.transformer.country_manufacturer) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Country manufacturer.';
                return;
            }
            if (!this.transformer.serial_no) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Serial number.';
                return;
            }
            if (!this.transformer.model_no) {
                this.validation_dialog = true;
               
                this.dialogMessage = 'Please provide Model number.';
                return;
            }
            this.save_loading = true;
            axios({
                url: process.env.VUE_APP_BASEURL + '/elements/' + this.element.id + '/transformers',
                method: 'POST',
                data: this.transformer,
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(response => {
                this.transformer = response.data;
                this.transformer_dialog = false;
                this.transformer_details = this.transformer;
                this.transformer = {};
                this.save_loading = false;
            }).catch(err => {
                alert(err.response);
            })
        },

        showLogDetails(item) {
            item.path = this.element.path; //Adding Element Path in the parameters
            console.log(item.path)
            this.attribute_value_details = item;
            console.log(this.attribute_value_details)
            this.showLogPopup = true;
        },
        hideLogPopup() {
            this.showLogPopup = false;
        },
        goToTransformer(t) {
            if (!t.latest_value) {
                this.validation_dialog = true;
                this.dialogMessage = 'No Data';
            return;
            }

             this.$router.push({ name: 'Home', params: { SelectTransformer: t.path  }});
        }
    }
}
</script>