import {Duvals } from './duvals';
export class Triangle5 extends Duvals
{
    getChartData(data_arr) {
        var chart_data = [];
        data_arr.forEach((data, idx) => {
            var c2h4 = data.c2h4;
            var c2h6 = data.c2h6;
            var ch4 = data.ch4;
            var timestamp = data.timestamp;
            var interpretation = data.interpretation;

            var tr_l = 100; //Equilateral triangle side length = 100, Origin at 33.33% of length of each side
            var tr_h = Math.sqrt(Math.pow(tr_l, 2) - Math.pow(tr_l / 2, 2)); //86.60254038
            var tr_ymax = (tr_l / 2) / Math.cos((30/180) * Math.PI); //57.73502692
            var tr_ymin = tr_ymax - tr_h;  //-28.86751346


            var dt1_total = c2h4 + c2h6 + ch4; //Getting the percentage of each value
            var c2h4_pc = (c2h4 / dt1_total) * 100;
            var c2h6_pc = (c2h6 / dt1_total) * 100;
            var ch4_pc = (ch4 / dt1_total) * 100;

            var dt_x = (c2h4_pc - c2h6_pc) * Math.sin(30 * Math.PI / 180);
            var dt_y = (ch4_pc / 100) * tr_h + tr_ymin;
            
            var chart_data_obj = {
                key : idx,
                x : dt_x,
                y : dt_y,
                c2h4 : c2h4,
                c2h6 : c2h6,
                ch4 : ch4,
                c2h4_pc : c2h4_pc,
                c2h6_pc : c2h6_pc,
                ch4_pc : ch4_pc,
                date : timestamp,
                interpretation : interpretation,
                color : this.getHexColor(idx, data_arr.length),
                radius : (idx < data_arr.length - 1 ? 2.5 : 4)
            };

            if (interpretation !== 'Normal') {
                chart_data.push(chart_data_obj);
            }
            // chart_data.push(chart_data_obj);
        });

        return chart_data;
    }

    load(data) {
        this.d3 = this.getD3();
        var color = {
            PD : 'pink',
            O : 'purple',
            T2 : 'LimeGreen',
            T3 : 'HotPink',
            S : 'blue',
            C : 'SkyBlue',
            ND : '#679A00',
        };

        var div_id = this.container.replace("#", "");
        
        var points = [
            {"x":0, "y":57.73502692}, //1
            {"x":-1.25, "y":55.57}, //2
            {"x":-7.5, "y":44.745}, //3
            {"x":-7.5, "y":43.013}, //4
            {"x":-0.75, "y":54.704}, //5
            {"x":-2.5, "y":36.084}, //6
            {"x":5, "y":49.075}, //7
            {"x":-1, "y":38.682}, //8
            {"x":11.5, "y":17.032}, //9
            {"x":17.5, "y":27.424}, //10
            {"x":19, "y":4.0415}, //11
            {"x":18, "y":2.3094}, //12
            {"x":28, "y":-15.01}, //13
            {"x":20, "y":-28.86751346}, //14
            {"x":50, "y":-28.86751346}, //15
            {"x":-10, "y":23.094}, //16
            {"x":2.5, "y":1.4434}, //17
            {"x":-15, "y":-28.86751346}, //18
            {"x":-27, "y":10.97}, //19
            {"x":-22, "y":2.3094}, //20
            {"x":-40, "y":-28.86751346}, //21
            {"x":-50, "y":-28.86751346} //22   
               
         ];

        var orders = [
            {"name":"PD", "order":[2, 3, 4, 5]},
            {"name":"O", "order":[1, 2, 5, 4, 6, 7]},
            {"name":"T2", "order":[7, 8, 9, 10]},
            {"name":"T3", "order":[10, 9, 11, 12, 13, 14, 15]},
            {"name":"T3", "order":[17, 18, 14]},
            {"name":"O", "order":[19, 22, 21, 20]},
            {"name":"S", "order":[3, 19, 20, 6]},
            {"name":"C", "order":[8, 16, 14, 13, 12, 11]},
            {"name":"ND", "order":[16, 21, 18, 17]}
        ];

        var zones = [];

        orders.forEach(function (o) {
            var polygon_pts = [];
            o.order.forEach(function (z) {
                polygon_pts.push({"x" : points[z - 1].x, "y" : points[z - 1].y});
            });

            zones.push({name : o.name, points : polygon_pts});
        });

        var margin = {top: 0, right: 0, bottom: 0, left: 0};
        var width = 438.8 - margin.left - margin.right;
        var height = 380 - margin.top - margin.bottom;
        var svg = this.d3.select('#' + div_id).append("svg")
            .attr("preserveAspectRatio", "xMinYMin meet")
            .attr("viewBox", "0 0 438.8 380")

        this.chart = svg.append("g").attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        this.xScale = this.d3.scaleLinear().range([0,width]);
        this.yScale = this.d3.scaleLinear().range([height,0]);

        this.xScale
            .domain([-60, 60])
            .nice();
        this.yScale
            .domain([-35, 70])
            .nice();
        this.chart.selectAll("polygon2")
            .data(zones)
            .enter().append("polygon")
            .attr("points", d => { return d.points.map(d => { 
                    return [this.xScale(d.x),this.yScale(d.y)].join(","); 
                }).join(" ");})
            .attr("fill", d => {
                return color[d.name];
            })
        //.attr("stroke","#666")
            .attr("stroke-width",2);

            svg.append("svg:text")
            .attr("x", 320)
            .attr("y", -190)
            .attr("transform", "rotate(60)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% C2H4")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");

        svg.append("svg:text")
            .attr("x", 215)
            .attr("y", 365)
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% C2H2")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");

        svg.append("svg:text")
            .attr("x", -105)
            .attr("y", 191)
            .attr("transform", "rotate(-60)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% CH4")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");



        this.tooltip = this.d3.select('body')
            .append('div')
            .attr('id', div_id + '-tooltip')
            .style('padding', '5px')
            .style('font-size', '10px')
            .style('background', '#fff')
            .style('opacity', 0)
            .style('position', 'absolute')
            .style('visibility', 'hidden')
            .style('font-family', 'calibri')
            .style('border', '1px solid #cccccc');

        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", d => {return d.radius})
            .attr("cx", d => { return this.xScale(d.x); })
            .attr("cy", d => { return this.yScale(d.y); })
            .attr("id", d => { return "plot-" + div_id + "-" + d.key; })
            .style("opacity", 0.2)
            .style("fill", d => { d.color; })
            .on("mouseover", (event, d) => {
                var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
                html += '<tr><td style="font-weight:bold;">Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H4 </td><td>' + d.c2h4.toFixed(2) + '('+ d.c2h4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H6 </td><td>' + d.c2h6.toFixed(2) + '('+ d.c2h6_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';
                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");
            })
            .on("mouseout", d => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0)
                    .style("visibility", "hidden");
            });

        var duration = 1000 / chart_data.length;
        chart_data.forEach((val, idx) => {
            var plot = this.d3.select('#plot-' + div_id + '-' + idx).transition()
                .duration((idx + 1) * duration)
                .style('fill', val.color)
                .style("opacity", 1)
                .style("cursor", "pointer");

                if(idx == chart_data.length - 1) {
                    plot.attr('r', 4);
                }
        });
    }

    updateData(data) {
        this.chart.selectAll("circle").remove();
        var div_id = this.container.replace("#", "");
        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", 2.4)
            .attr("cx", d => { return this.xScale(d.x); })
            .attr("cy", d => { return this.yScale(d.y); })
            .attr("id", function (d) { return "plot-" + div_id + "-" + d.key; })
            .style("fill", d => { return d.color })
            .style("cursor", "pointer")
            .on("mouseover", (event, d) => {
                var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
                html += '<tr><td style="font-weight:bold;">Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H4 </td><td>' + d.c2h4.toFixed(2) + '('+ d.c2h4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H6 </td><td>' + d.c2h6.toFixed(2) + '('+ d.c2h6_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';
                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");
            })
            .on("mouseout", d => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0)
                    .style("visibility", "hidden");
            });

            this.d3.select("#plot-" + div_id + "-" + (chart_data.length - 1)).transition().duration(100).attr('r', 4);
    }
}