<template >
  <div>
    <v-navigation-drawer v-model="drawer" dark src="@/assets/sidebar-bg.png" app>
      <v-list-item>
        <v-list-item-content class="custom-content text-center mt-5 mb-2">
          <v-list-item-title>
            <img src="@/assets/logo.png" alt="" width="80">
          </v-list-item-title>
          <v-list-item-subtitle>
            Dissolved Gas Analysis
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list dense nav>
        <v-list-item-group v-model="page">
          <v-list-item link v-for="(i, idx) in items" :key="idx" :to="{ name: i.link }" @click="title = i.title">
            <v-list-item-icon>
              <v-icon>{{ i.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ i.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>

        <v-list-item @click="logout()">
          <v-list-item-icon>
            <v-icon>mdi-power</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Log-Out</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
     
      </v-list>
      <template v-slot:append>
        <v-list-item @click="darkmode()">
          <v-list-item-icon>
            <v-icon v-if="$vuetify.theme.dark">mdi-weather-night</v-icon>
            <v-icon v-if="!$vuetify.theme.dark">mdi-weather-sunny</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title v-if="$vuetify.theme.dark">Dark</v-list-item-title>
            <v-list-item-title v-if="!$vuetify.theme.dark">Light</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-navigation-drawer>

    
    <v-app-bar app dark class="red app-header">
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>{{ title }}</v-toolbar-title>
    </v-app-bar>
    <v-main>
      <router-view ></router-view>
    </v-main>

      <!-- Footer -->
      <v-footer class="footer mt-10 mb-1" color="transparent">
      <span class="lighten-2"> {{ new Date().getFullYear() }} — &copy; Calibr8 Systems Inc.</span>
    </v-footer>
  </div>
</template>

<style scoped>
.custom-content {
  flex: unset;
}
/* Center the text in the footer */
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #999;
  font-size: 14px;
}
  
  /* Make the text lighter */

</style>
<script>
export default {

  data: () => ({
    drawer: null,
    page: 0,
    title: "",
    user: {},
    items: []
  }),
  mounted() {
    this.user = JSON.parse(localStorage.getItem('user')).user;
    // console.log(this.user);
    if (localStorage.getItem('dark-mode') === 'true') {
      this.$vuetify.theme.dark = true;
    }
    if (this.user.account_level == 'company_admin') {
      this.items = [{
        title: 'Dashboard',
        icon: 'mdi-view-dashboard-variant',
        link: 'Home'
      },
      {
        title: 'Assets',
        icon: 'mdi-cube',
        link: 'Assets'
      },
      {
        title: 'Subscription',
        icon: 'mdi-handshake',
        link: 'Subscription'
      },
      {
        title: 'Users',
        icon: 'mdi-account',
        link: 'Users'
      }];
    }
    else {
      this.items = [{
        title: 'DGA Dashboard',
        icon: 'mdi-view-dashboard-variant',
        link: 'Home'
      },
      {
        title: 'Assets',
        icon: 'mdi-cube',
        link: 'Assets'
      }];
    }
  },
  watch: {
    '$vuetify.theme.dark': {
      handler: function (val) {
        localStorage.setItem('dark-mode', val);
      },
      deep: true
    }
  },
  methods: {
    logout() {
      localStorage.removeItem('user');
      this.$router.push({ name: 'Welcome' });
    },
    darkmode(){
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // set title based on the route name
      vm.title = to.name
    })
  }
}
</script>