<template>
    <div id="pentagon1">
    </div>
</template>

<script>
    import { Pentagon1 } from '@/components/configs/pentagon1.js'
    export default {
        name : 'DuvalsPentagonOne',
        props : ['data'],
        data() {
            return {
                duvals : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.p1;
                return d;
            });

            this.duvals = new Pentagon1('pentagon1');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.p1;
                    return d;
                });

                this.duvals.updateData(data);
            }
        }
    }
</script>