<template>
    <div class="background">
        <video autoplay muted loop class="vid-background">
            <source src="@/assets/video-bg.mp4" type="video/mp4">
        </video>
        <div class="banner" :class="show_ads ? 'expand' : 'collapse'">
            <div class="vid-advertisement">
                <v-container>
                    <div class="text-right">
                        <v-btn x-small text dark @click="show_ads = !show_ads">Close</v-btn>
                    </div>
                    <v-row>
                        <v-col cols="12" sm="5">
                            <div class="white--text text-justify">
                                <h3>What is C<span class="red--text">8</span> Vortex?</h3>
                                Vortex is an transmission and distribution asset monitoring suite that equips users to monitor and in realtime using built-in analytics, to crucial equipment for maintaining high efficiency, productivity and lowering breakdown instances. It provides a monitoring module for dissovled gas analysis, central in which is the duval's diagnostic suite which is able to provide critical and accurate insight on your equipment health and help you make decisions on maintenance activities before any major issues are encountered!"

                            </div>
                        </v-col>
                        <v-col cols="12" sm="7">
                            <video height="300" ref="ads" controls>
                                <source src="@/assets/dga.mp4" type="video/mp4">
                            </video>
                        </v-col>
                    </v-row>
                </v-container>
            </div>
            <!-- <div class="border"></div> -->
            <img src="@/assets/banner.png" height="55" alt="">
        </div>
        <div class="container pa-10 mt-30">
            <v-row>
                <v-col cols="12" sm="4">
                    <v-card>
                        <v-img
                            height="200"
                            src="@/assets/7.png"
                            >
                        </v-img>
                        <v-card-title>WHAT IS C8 VORTEX?</v-card-title>
                        <v-card-text>
                            <div class="scrollbar text-justify">
                                C8 Vortex is a transmission and distribution asset monitoring suite that allows users to monitor and analyze critical equipment in real time using built-in analytics to maintain high efficiency, increase productivity, and reduce breakdowns. It includes a monitoring module for dissolved gas analysis, along with Duval's diagnostic suite, which can provide critical and accurate insight into your equipment's health and empower you to make maintenance decisions before any major issues arise!
                            </div>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn class="darken-2 mb-1"  block outlined color="indigo darken-2" rounded @click="show_ads = !show_ads">
                                <v-icon left>mdi-information-variant</v-icon> Explore
                            </v-btn>
                            <!-- <v-btn small block color="grey" class="darken-2 white--text" outlined rounded @click="show_ads = !show_ads">
                                <v-icon left>mdi-information-variant</v-icon> Explore
                            </v-btn> -->
                        </v-card-actions>
                    </v-card>
                </v-col>
                <v-col cols="12" sm="4">
                    <v-card>
                        <v-img
                            height="200"
                            src="@/assets/2.png"
                            >
                        </v-img>
                        <v-card-title>TAKE THE NEXT STEP</v-card-title>
                        <v-card-text>
                            <div class="scrollbar text-justify">
                                Improve your transformer maintenance practice today. Unlock operational insights and drive efficiency for your mission-critical processes with our Basic and Advanced subscriptions! <br/><br/>Not yet ready to commit to a long-term partnership? You try out the demo version to see what works best for your organization. 
                            </div>
                        </v-card-text>
                        <v-card-actions>
                            <div>
                                <stripe-checkout
                                ref="checkoutRef"
                                mode="payment"
                                :pk="key"
                                :line-items="items"
                                :success-url="success_url"
                                :cancel-url="cancel_url"
                                :client-reference-id="reference_id"
                                @loading="v => loading = v"
                                />
                            </div>
                            <v-btn class="darken-2 mb-1"  block outlined color="orange darken-2" rounded  @click="showSubscriptions()">
                                <v-icon>mdi-gesture-tap</v-icon> Subscribe
                            </v-btn>
                            <!-- <v-btn color="orange" rounded dark class="darken-2" block @click="showSubscriptions()">
                                <v-icon>mdi-gesture-tap</v-icon> Subscribe
                            </v-btn> -->
                        </v-card-actions>
                    </v-card>
                </v-col>
                <v-col cols="12" sm="4">
                    <v-card>
                        <v-img
                            height="200"
                            src="@/assets/3.png"
                            >
                        </v-img>
                        <v-card-title>GET STARTED</v-card-title>
                        <v-card-text>
                            <div class="scrollbar text-justify" >
                                Welcome to C8 Vortex. Login or Sign up for an account and extend the life of your critical equipment with an intelligent transmission and distribution asset monitoring suite.
                            </div>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn class="darken-2 mb-1"  block outlined color="red darken-2" rounded  @click="showLogin()" v-if="!authenticated_user">
                                <v-icon left>mdi-login</v-icon>
                                LOG-IN/SIGN-UP
                            </v-btn>
                            <v-btn class="darken-2 mb-1"  block outlined color="red darken-2" rounded  @click="enter()" v-if="authenticated_user">
                                <v-icon left>mdi-login</v-icon>
                                Enter
                            </v-btn>
                            <!-- <v-btn block @click="showLogin()" rounded color="red darken-2" dark v-if="!authenticated_user">
                                <v-icon left>mdi-login</v-icon>
                                LOG-IN/SIGN-UP
                            </v-btn>
                            <v-btn block @click="enter()" rounded color="red darken-2" dark v-if="authenticated_user">
                                <v-icon left>mdi-login</v-icon>
                                Enter
                            </v-btn> -->
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
        </div>
        <Login :show="login_dialog" :login_dialog.sync="login_dialog" @authenticate="authenticate"/>
        <subscriptions-dialog :dialog="subscriptions_dialog" 
            @close="closeSubscriptionsDialog"
            @subscribe="subscribe"/>
        <company-details-dialog 
            :dialog="company_details_dialog" 
            :details="company_details"
            @update="updateProfile"/>
        <subscription-details 
            @close="closeCurrentSubscriptionDetailsDialog"
            :dialog="current_subscription_details_dialog"
            :details="authenticated_user"/>
    </div>
</template>

<style scoped>
.mt-30 {
        margin-top : 100px;
    }
    .background {
        /* background : url(@/assets/background.jpg) no-repeat; */
        background : #000;
        background-size: cover;
        width : 100%;
        height : 100%;
    }
    .vid-background {
        position: fixed;
        min-width: 100%;
        min-height : 100%; 
        z-index : 0; 
        top: 0px;
        opacity : 0.50;
    }
    .vid-advertisement {
        padding : 30px 0 30px;
        background-image: url(@/assets/advertisement-cont.png);
        background-repeat: repeat;
    }
    
    .scrollbar {
        height: 150px;
        overflow-y: scroll;
        font-size: 13px;
        /* Add the ability to scroll */
    }

    /* Hide scrollbar for Chrome, Safari and Opera */
    .scrollbar::-webkit-scrollbar {
        display: none;
    }

    /* Hide scrollbar for IE, Edge and Firefox */
    .scrollbar {
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }
    .banner {
        text-align : center;
        position: fixed;
        top : 0px;
        z-index : 10;
        width : 100%;
        transition: 0.5s ease-in-out;
    }
    .banner.collapse {
        transform: translateY(-385px);
    }
    .banner.expand {
        transform : translateY(0px);
    }
    .banner .border {
        background : #000;
        opacity : 0.8;
        height : 20px;
    }
</style>
<script>
    import { StripeCheckout } from '@vue-stripe/vue-stripe';
    import Login from '@/components/Login.vue';
    import SubscriptionsDialog from '@/components/popups/SubscriptionsDialog.vue';
    import CompanyDetailsDialog from '@/components/popups/CompanyDetailsDialog.vue';
    import SubscriptionDetails from '@/components/popups/SubscriptionDetails.vue';
    import axios from 'axios';
    export default {
        name : 'Subscriptions',
        components : {
            Login,
            StripeCheckout,
            SubscriptionsDialog,
            CompanyDetailsDialog,
            SubscriptionDetails
        },
        watch : {
            'show_ads'() {
                if(!this.show_ads) {
                    this.$refs.ads.pause();
                }
                else {
                    this.$refs.ads.play();
                }
            }
        },
        data() {
            return {
                show_ads : false,
                login_dialog : false,
                loading: false,
                items: [],
                success_url: null,
                cancel_url: 'http://localhost:8080/',
                key : process.env.VUE_APP_STRIPE_KEY,
                subscriptions_dialog : false,
                authenticated_user : null,
                company_details_dialog : false,
                company_details : {},
                reference_id : null,
                current_subscription_details : false,
                current_subscription_details_dialog : false
            }
        },
        mounted() {
            if(localStorage.getItem('user')) {
                axios({
                    url : process.env.VUE_APP_BASEURL + '/profile',
                    method : 'GET',
                    headers : {
                        'Content-Type' : 'application/x-www-form-urlencoded',
                        'Authorization' : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                    }
                }).then(res => {
                    this.authenticated_user = res.data;
                }).catch(err => {
                    this.authenticated_user = null;
                })
            }
        },
        methods : {
            goTo(name) {
                this.$router.push({name : name})
            },
            showLogin() {
                this.login_dialog = true;
            },
            closeLoginDialog () {
                this.login_dialog = false;
            },
            authenticate(type, response) {
                // console.log(response.zc.id_token)
                if(type == 'google') {
                    let token = response.zc != undefined ? response.zc.id_token : response.Bc.id_token;
                    axios({
                        method : 'POST',
                        url : process.env.VUE_APP_BASEURL + '/google/auth',
                        data : {id_token : token},
                        headers : {'Content-Type' : 'application/x-www-form-urlencoded'}
                    }).then(res => {
                        window.localStorage.setItem('user', JSON.stringify(res.data));
                        this.authenticated_user = res.data;
                        this.closeLoginDialog();

                        if(res.data.user.company.updated == 1) {
                            if(res.data.user.subscription) {
                                if(this.current_subscription_details) {
                                    this.current_subscription_details_dialog = true;
                                }
                                else {
                                    this.$router.push({name : 'Home'});
                                }
                            }
                            else {
                                this.subscriptions_dialog = true;
                            }
                        }
                        else {
                            this.company_details = res.data.user;
                            this.company_details_dialog = true;
                        }
                    }).catch(err => {
                        console.log(err);
                    });
                }
                else {
                    axios({
                        method : 'POST',
                        url : process.env.VUE_APP_BASEURL + '/microsoft/auth',
                        data : {token : response.accessToken},
                        headers : {'Content-Type' : 'application/x-www-form-urlencoded'}
                    }).then(res => {
                        window.localStorage.setItem('user', JSON.stringify(res.data))
                    }).catch(err => {
                        console.log(err);
                    });
                }
            },
            closeCurrentSubscriptionDetailsDialog() {
                this.current_subscription_details_dialog = false;
            },
            updateProfile(profile) {
                axios({
                    url : process.env.VUE_APP_BASEURL + '/profile',
                    method : 'PUT',
                    data : profile,
                    headers : {
                        'Content-Type' : 'application/x-www-form-urlencoded',
                        'Authorization' : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                    }
                }).then(res => {
                    this.company_details_dialog = false;
                    this.subscriptions_dialog = true;
                }).catch(err => {
                    alert(JSON.stringify(err));
                });
            },
            showSubscriptions() {
                if(this.authenticated_user) {
                    this.current_subscription_details_dialog = true;
                }
                else {
                    this.current_subscription_details = true;
                    this.showLogin();
                }
            },
            closeSubscriptionsDialog() {
                this.subscriptions_dialog = false;
            },
            subscribe(type, price_id) {
                this.items = [
                    {
                        price : price_id,
                        quantity : 1
                    }
                ];
                this.subscriptions_dialog = false;

                this.success_url = process.env.VUE_APP_BASEURL + '/payment/success?r=thank-you&session_id={CHECKOUT_SESSION_ID}';
                this.cancel_url = process.env.VUE_APP_CLIENTURL + '/';
                this.reference_id = JSON.parse(localStorage.getItem('user')).user.id.toString() + ':' + type;
                this.$refs.checkoutRef.redirectToCheckout();
            },
            enter() {
                this.$router.push({name : 'Home'})
            }
        }
    }
</script>
