<template>
    <v-row justify="center">
      <v-dialog
        v-model="dialog"
        persistent
        max-width="290"
        @click:outside="close()"
      >
        <v-date-picker
            v-model="selected_date"
            color="indigo darken-2"
            @change="getDate()"
            >
            <v-spacer></v-spacer>
            <!-- <v-btn
                dark
                color="red"
                small
                left
                @click="setDateNow()"
            >
                Set To Now
            </v-btn> -->
        </v-date-picker>
      </v-dialog>
    </v-row>
  </template>

<script>
import { timeMixin } from '@/mixins/timeMixin.js'
export default {
    name : 'DatePicker',
    props : ['dialog', 'date', 'time'],
    mixins : [timeMixin],
    data() {
        return {
            selected_date : null,
            duvals : null
        }
    },
    watch : {
        'date'() {
            this.selected_date = this.date
        }
    },
    mounted() {
        
    },
    methods : {
        setDateNow() {
            this.$emit('setDate', this.formatDate(new Date().toLocaleString()), true);
        },
        getDate() {
            this.$emit('startDate', this.selected_date, false);
        },
        getDate() {
            this.$emit('setDate', this.selected_date, false);
        },
        close() {
            this.$emit('close');
        }
    }
}
</script>