import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import GAuth from 'vue-google-oauth2'
import { default as msalPlugin } from "vue-msal-browser";

const msalConfig = {
  auth: {
    // tenant: 'b4aa6353-ea34-40d9-9fd6-9bf747f0e64a',
    tenant: 'b4aa6353-ea34-40d9-9fd6-9bf747f0e64a',
    // clientId: '9e5fd16a-f1e6-4f48-863b-5fb7baafaec9',
    clientId: '567f5ca5-9d6f-41b1-86cb-8527e3a991b0',
    authority: 'https://login.microsoftonline.com/organizations/',
    redirectUri: 'http://localhost:8080/', // It has to be configured on your Azure tenant
    scopes: []
  },
  cache: {
    cacheLocation: 'localStorage'
  },
  graph: {
    url: 'https://graph.microsoft.com/v1.0/me',
    scopes: [ "User.Read", "User.Write", "Calendar.Read", "User.ReadBasic.All", "User.Read.All"],
    response_type: "blob"
  },
  mode: "popup"
}
Vue.use(msalPlugin, msalConfig);

const gAuthOptions = { 
  clientId: '198378287573-aukr99qrcbqa64mq73j5kho2hh6sc1dv.apps.googleusercontent.com', 
  scope: 'email', 
  prompt: 'select_account',
  plugin_name:'Google Authentication',
  include_granted_scopes: true,
  fetch_basic_profile: true 
}


Vue.use(GAuth, gAuthOptions)

Vue.config.productionTip = false
new Vue({
  router,
  store,
  vuetify,
  render: function (h) { return h(App) }
}).$mount('#app')
