<template>
    <div id="triangle6">
    </div>
</template>

<script>
    import { Triangle6 } from '@/components/configs/duvalst6.js'
    export default {
        name : 'DuvalsTriangleSix',
        props : ['data'],
        data() {
            return {
                duvals : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.t6;
                return d;
            });

            this.duvals = new Triangle6('triangle6');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.t6;
                    return d;
                });

                this.duvals.updateData(data);
            }
        }
    }
</script>