<template>
    <div>
        <div class="pa-3">
            <v-card>
                <v-toolbar dark dense color="red">
                    Users List
                </v-toolbar>
                    <v-data-table
                        :headers="headers"
                        :items="users"
                        item-key="name"
                        class="elevation-1"
                    >
                    <template v-slot:item="{item}">
                        <tr>
                        <td class="font-sm">
                            {{ item.name }}
                        </td>
                        <td class="font-sm">
                            {{ item.email }}
                        </td>
                        <td class="font-sm">
                            {{ item.account_type }}
                        </td>
                        <td class="font-sm">
                            {{ item.account_level }}
                        </td>
                        <td class="font-sm">
                            {{ item.created_at }}
                        </td>
                        <td class="font-sm">
                            <v-btn :disabled="item.account_level == 'company_admin'" block x-small @click="viewAssets(item)">Assets</v-btn>
                        </td>
                        </tr>
                    </template>
                </v-data-table>
            </v-card>
        </div>

        <v-dialog width="400" v-model="assets_dialog">
            <v-progress-linear
                v-if="saving"
                indeterminate
                color="red darken-2"
                >
            </v-progress-linear>
            <v-row no-gutters>
                <v-col >
                    <v-list dense>
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title class="text-h6">
                                    Select Assets for <span class="red--text uppercase">{{ user.name }}</span>
                                </v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                        <v-divider></v-divider>
                        <v-list-item v-for="e in elements" :key="e.id" @click="toggleUserAsset(e.id)">
                            <v-btn 
                                :color="user_elements.indexOf(e.id) >= 0 ? 'red' : 'grey'" 
                                small icon>
                                <v-icon>mdi-check-circle</v-icon>
                            </v-btn>
                            &nbsp;
                            <v-list-item-content>
                                 {{ e.path }}
                            </v-list-item-content>
                        </v-list-item>
                        <v-divider></v-divider>
                        <v-list-item>
                            <v-spacer></v-spacer>
                            <v-btn small @click="assets_dialog = false">Close</v-btn>
                        </v-list-item>
                    </v-list>
                </v-col>
            </v-row>
        </v-dialog>
    </div>
</template>
<style scoped>
    .uppercase {
        text-transform : uppercase;
    }
    .v-application .grey--text {
        color : #d9d9d9 !important;
    }
    .text-h6 {
        font-size : 16px !important;
    }
</style>
<script>
    import axios from 'axios';
    export default {
        name : 'Users',
        data () {
            return {
                elements : [],
                assets_dialog : false,
                users : [],
                user : {},
                user_elements : [],
                saving : false,
                headers : [
                    {
                        text: 'Name',
                        align: 'start',
                        sortable: true,
                        value: 'name',
                    },
                    {
                        text: 'Email',
                        align: 'start',
                        sortable: true,
                        value: 'email',
                    },
                    {
                        text: 'Account Type',
                        align: 'start',
                        sortable: true,
                        value: 'account_type',
                    },
                    {
                        text: 'Access Level',
                        align: 'start',
                        sortable: true,
                        value: 'account_level',
                    },
                    {
                        text: 'Registration Date',
                        align: 'start',
                        sortable: true,
                        value: 'created_at',
                    },
                    {
                        text: 'Assets Permissions',
                        align: 'start',
                        sortable: false,
                    },
                ]
            }
        },
        mounted() {
            axios({
                url : process.env.VUE_APP_BASEURL + '/users',
                method : 'GET',
                headers : {
                    Authorization : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                }
            }).then(res => {
                this.users = res.data.map(u => {
                    u.created_at = new Date(u.created_at).toLocaleDateString();
                    return u;
                });
            }).catch(err => {
                alert(JSON.stringify(err.response))
            })
        },
        methods: {
            viewAssets(user) {
                this.user = user;
                this.assets_dialog = true;

                axios({
                    url : process.env.VUE_APP_BASEURL + '/users/' + user.id + '/elements',
                    method : 'GET',
                    headers : {
                        Authorization : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                    }
                }).then(res => {
                    this.elements = res.data.elements.map(e => {
                        e.path = e.path.replaceAll('\\', ' > ');

                        return e;
                    });

                    this.user_elements = res.data.user_elements.map(e => {
                        return e.element_id;
                    });

                    console.log(this.user_elements);
                });
            },
            toggleUserAsset(id) {
                this.saving = true;
                let idx = this.user_elements.indexOf(id);
                if(idx == -1) { //Add Element
                    axios({
                        url : process.env.VUE_APP_BASEURL + '/users/' + this.user.id + '/elements',
                        method : 'POST',
                        data : {
                            element_id : id,
                            action : 'add'
                        },
                        headers : {
                            Authorization : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                        }
                    }).then(res => {
                        this.user_elements.push(id);
                        this.saving = false;
                    }).catch(err => {
                        alert(JSON.stringify(err.response));
                        this.saving = false;
                    });
                }
                else { //Delete Element
                    axios({
                        url : process.env.VUE_APP_BASEURL + '/users/' + this.user.id + '/elements',
                        method : 'POST',
                        data : {
                            element_id : id,
                            action : 'delete'
                        },
                        headers : {
                            Authorization : 'Bearer ' + JSON.parse(localStorage.getItem('user')).token
                        }
                    }).then(res => {
                        this.user_elements.splice(idx, 1);
                        this.saving = false;
                    }).catch(err => {
                        alert(JSON.stringify(err.response));
                        this.saving = false;
                    });
                }
            }
        }
    }
</script>