import { Duvals } from './duvals'
export class Pentagon2 extends Duvals {
    getChartData(data) {
        var chart_data = [];

        data.forEach((d, idx) => {
            var c2h6 = d.c2h6;
            var h2 = d.h2;
            var c2h2 = d.c2h2;
            var c2h4 = d.c2h4;
            var ch4 = d.ch4;
            var timestamp = d.timestamp;
            var interpretation = d.interpretation;

            var tot_gas = c2h6 + h2 + c2h2 + c2h4 + ch4;
            var c2h6_pc = (c2h6 / tot_gas) * 100;
            var h2_pc = (h2 / tot_gas) * 100;
            var c2h2_pc = (c2h2 / tot_gas) * 100;
            var c2h4_pc = (c2h4 / tot_gas) * 100;
            var ch4_pc = (ch4 / tot_gas) * 100;

            var c2h6_x = Math.cos(18 * Math.PI / 180) * c2h6_pc * (-1);
            var c2h6_y = Math.sin(18 * Math.PI / 180) * c2h6_pc;
            var h2_x = 0;
            var h2_y = Math.sin(90 * Math.PI / 180) * h2_pc;
            var c2h2_x = Math.cos(18 * Math.PI / 180) * c2h2_pc;
            var c2h2_y = Math.sin(18 * Math.PI / 180) * c2h2_pc;
            var c2h4_x = Math.cos(54 * Math.PI / 180) * c2h4_pc;
            var c2h4_y = Math.sin(54 * Math.PI / 180) * c2h4_pc * (-1);
            var ch4_x = Math.cos(54 * Math.PI / 180) * ch4_pc * (-1);
            var ch4_y = Math.sin(54 * Math.PI / 180) * ch4_pc * (-1);

            var a1 = (c2h6_x * h2_y) - (h2_x * c2h6_y);
            var a2 = (h2_x * c2h2_y) - (c2h2_x * h2_y);
            var a3 = (c2h2_x * c2h4_y) - (c2h4_x * c2h2_y);
            var a4 = (c2h4_x * ch4_y) - (ch4_x * c2h4_y);
            var a5 = (ch4_x * c2h6_y) - (c2h6_x * ch4_y);
            var a = (a1 + a2 + a3 + a4 + a5) / 2;

            var cx1 = (c2h6_x + h2_x) * a1;
            var cx2 = (h2_x + c2h2_x) * a2;
            var cx3 = (c2h2_x + c2h4_x) * a3;
            var cx4 = (c2h4_x + ch4_x) * a4;
            var cx5 = (ch4_x + c2h6_x) * a5;

            var cy1 = (c2h6_y + h2_y) * a1;
            var cy2 = (h2_y + c2h2_y) * a2;
            var cy3 = (c2h2_y + c2h4_y) * a3;
            var cy4 = (c2h4_y + ch4_y) * a4;
            var cy5 = (ch4_y + c2h6_y) * a5;

            var x = (cx1 + cx2 + cx3 + cx4 + cx5) / (6 * a);
            var y = (cy1 + cy2 + cy3 + cy4 + cy5) / (6 * a);


            // var chart_data = [{
            //     key : idx,
            //     x : x,
            //     y : y,
            //     date : "2019-01-01 00:00:01",
            //     cond : 1,
            //     dp2_fault : 'Test'
            // }];
            
            var chart_data_obj = {
                key : idx,
                x : x,
                y : y,
                c2h6_pc : c2h6_pc,
                h2_pc : h2_pc,
                c2h2_pc : c2h2_pc,
                c2h4_pc : c2h4_pc,
                ch4_pc : ch4_pc,
                c2h6 : c2h6,
                h2 : h2,
                c2h2 : c2h2,
                c2h4 : c2h4,
                ch4 : ch4,
                date : timestamp,
                interpretation : d.interpretation,
                radius : (idx < data.length - 1 ? 2 : 4),
                color : this.getHexColor(idx, data.length),
            }

            if (interpretation !== 'Normal') {
                chart_data.push(chart_data_obj);
            }
            // chart_data.push(chart_data_obj);
        })
        return chart_data;
    }
    load(data) {
        this.container = this.getContainer();
        this.div_id = this.container.replace('#', '');
        this.d3 = this.getD3();

        var color = this.d3.scaleOrdinal()
            .domain(["C", 
                    "T3H", 
                    "D2", 
                    "D1", 
                    "PD", 
                    "S", 
                    "O"])
            .range(["hotpink",
                    "blue",
                    "LimeGreen",
                    "#679A00",
                    "pink",
                    "purple",
                    "SkyBlue"]);

            //Adapted from: http://jsdatav.is/chap07.html
        var margin = {top: 0, right: 0, bottom: 0, left: 0},
            width = 600 - margin.left - margin.right,
            height = 600 - margin.top - margin.bottom;
        var svg = this.d3.select('#' + this.container).append("svg")
            .attr("preserveAspectRatio", "xMinYMin meet")
            .attr("viewBox", "0 0 535 500")
            // .attr("height", height + margin.left + margin.right)
            // .attr("width", width + margin.top + margin.bottom)
            // .attr("perserveAspectRatio", "xMinYMid");
        this.chart = svg.append("g")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        this.xScale = this.d3.scaleLinear()
            .range([0,width]);
        this.yScale = this.d3.scaleLinear()
            .range([height,0]);
        this.xScale.domain([-35, 50]).nice();
        this.yScale.domain([-60, 40]).nice();

        var arrayOfPolygons =  [
        {"name": "C",
            "points":[
                {"x":-21.5,"y":-32},
                {"x":-11,"y":-8},
                {"x":-3.6,"y":-3},
                {"x":2.5, "y":-32}
        ]
        },
        {"name": "T3H",
        "points":[
                {"x":24,"y":-30},
                {"x":23.6,"y":-32},
                {"x":2.5,"y":-32},
                {"x":-3.5, "y":-3},
                {"x":0,"y":-3}
        ]},
        {"name": "D2",
        "points":[
                {"x":4,"y":16},
                {"x":32,"y":-6},
                {"x":24,"y":-30},
                {"x":0, "y":-3}, //{"x":-1, "y":-2}
                {"x":0,"y":0} //{"x":-1,"y":0}
        ]},
        {"name": "D1",
        "points":[
                {"x":0,"y":40},
                {"x":38,"y":12},
                {"x":32,"y":-6},
                {"x":4, "y":16},
                {"x":0,"y":0}
        ]},
        {"name": "PD",
        "points":[
                {"x":0,"y":33},
                {"x":0,"y":24.5},
                {"x":-1,"y":24.5},
                {"x":-1, "y":33}
        ]},
        {"name": "S",
        "points":[
                {"x":-35,"y":3},
                {"x":-38,"y":12},
                {"x":0,"y":40},
                {"x":0, "y":33},
                {"x":-1, "y":33},
                {"x":-1, "y":24.5},
                {"x":0, "y":24.5},
                {"x":0, "y":1.5}
        ]},
        {"name": "O",
        "points":[
                {"x":-21.5,"y":-32},
                {"x":-23.6,"y":-32},
                {"x":-35,"y":3},
                {"x":0, "y":1.5},
                {"x":0,"y":-3},
                {"x":-3.5,"y":-3},
                {"x":-11,"y":-8}
        ]}
        ];


        this.chart.selectAll("polygon")
            .data(arrayOfPolygons)
            .enter().append("polygon")
            .attr("points",d => { 
                return d.points.map(d => { return [
                    this.xScale(d.x),
                    this.yScale(d.y)
                ].join(","); }).join(" ");
            })
            .attr("fill", d => {return color(d.name)})
            //.attr("stroke","#666")
            .attr("stroke-width",2);


        svg.append("line")
            .attr("style", "stroke:#808080;stroke-width:1px")
            .attr("x1", this.xScale(0))
            .attr("y1",this.yScale(0))
            //.attr("x2", this.xScale(40 * Math.cos(90)))
            //.attr("y2",this.yScale(40 * Math.sin(90)));
            .attr("x2", this.xScale(40 * Math.cos(90 / 180 * Math.PI)))
            .attr("y2",this.yScale(40 * Math.sin(88 / 180 * Math.PI)));

        svg.append("line")
        .attr("style", "stroke:#808080;stroke-width:1px")
        .attr("x1", this.xScale(0))
        .attr("y1",this.yScale(0))
        .attr("x2", this.xScale(40 * Math.cos(18.5 / 180 * Math.PI)))
        .attr("y2",this.yScale(40 * Math.sin(17.5 / 180 * Math.PI)));

        svg.append("line")
        .attr("style", "stroke:#808080;stroke-width:1px")
        .attr("x1", this.xScale(0))
        .attr("y1",this.yScale(0))
        .attr("x2", this.xScale(40 * Math.cos(54 / 180 * Math.PI)))
        .attr("y2",this.yScale(-40 * Math.sin(53 / 180 * Math.PI)));

        svg.append("line")
        .attr("style", "stroke:#808080;stroke-width:1px")
        .attr("x1", this.xScale(0))
        .attr("y1",this.yScale(0))
        .attr("x2", this.xScale(-40 * Math.cos(54 / 180 * Math.PI)))
        .attr("y2",this.yScale(-40 * Math.sin(53 / 180 * Math.PI)));
        
        svg.append("line")
        .attr("style", "stroke:#808080;stroke-width:1px")
        .attr("x1", this.xScale(0))
        .attr("y1",this.yScale(0))
        .attr("x2", this.xScale(-40 * Math.cos(18.5 / 180 * Math.PI)))
        .attr("y2",this.yScale(40 * Math.sin(17.5 / 180 * Math.PI)));

        svg.append("svg:text")
        .attr("x", 48)
        .attr("y", 135)
        .attr("transform", "rotate(-39.4)")
        .attr("text-anchor", "middle")
        .text("40% C2H6")
        .style('font-family', 'calibri')
        .style("font", "sans-serif")
        .style("font-size", "12px")
        .style("fill", "green")
        .style("stroke-width", "0px");

        svg.append("svg:text")
            .attr("x", 360)
            .attr("y", -165)
            .attr("transform", "rotate(33.75)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "12px")
            .text("40% H2")
            .style('font-family', 'calibri')
            .style("fill", "green")
            .style("stroke-width", "0px");

        svg.append("svg:text")
            .attr("x", -160)
            .attr("y", 555)
            .attr("transform", "rotate(-75)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("40% C2H2")
            .style('font-family', 'calibri')
            .style("fill", "green")
            .style("stroke-width", "0px");

        svg.append("svg:text")
            .attr("x", 255)
            .attr("y", 454)
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "12px")
            .text("40% C2H4")
            .style('font-family', 'calibri')
            .style("fill", "green")
            .style("stroke-width", "0px");

        svg.append("svg:text")
            .attr("x", 310)
            .attr("y", 65)
            .attr("transform", "rotate(70)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("40% CH4")
            .style('font-family', 'calibri')
            .style("fill", "green")
            .style("stroke-width", "0px");
            
            this.tooltip = this.d3.select("body")
            .append("div")  
            .attr("class", "tooltip")
            .attr("id", "tooltip-" + this.div_id)
            .style("padding", "3px")
            .style("background", "#fff")
            .style("position", "absolute")
            .style("opacity", 0)
            .style('font-family', 'calibri')
            .style("border", "1px solid #cccccc");
            
            


            //if (chart_data && chart_data.length !== undefined /*&& chart_data.length > 0*/) {

        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
        .data(chart_data)
        .enter().append("circle")
        .attr("r", d => {return d.radius; })
        .attr("cx", d => { return this.xScale(d.x); })
        .attr("cy", d => { return this.yScale(d.y); })
        .attr("id", d => { return "plot-" + this.div_id + "-" + d.key; })
        .style("stroke", d => { return d.color; })
        .style("stroke-width", 2)
        .style("fill", d => { return d.color; })
        // .style("visibility", "hidden")
        .on("mouseover", (event, d) => {
            // var region = getRegion([d.x, d.y], duvT1Zones);
            var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
            html += '<tr><td>Timestamp </td><td>' + d.date + '</td></tr>';
            html += '<tr><td>C2H6 </td><td>' + d.c2h6 + '('+ d.c2h6_pc.toFixed(2) +'%)' + '</td></tr>';
            html += '<tr><td>H2 </td><td>' + d.h2 + '('+ d.h2_pc.toFixed(2) +'%)' + '</td></tr>';
            html += '<tr><td>C2H4 </td><td>' + d.c2h4 + '('+ d.c2h4_pc.toFixed(2) +'%)' + '</td></tr>';
            html += '<tr><td>C2H2 </td><td>' + d.c2h2 + '('+ d.c2h2_pc.toFixed(2) +'%)' + '</td></tr>';
            html += '<tr><td>CH4 </td><td>' + d.ch4 + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
            html += '<tr><td>Fault </td><td>' + d.interpretation + '</td></tr>';
            html += '</table>';
            this.tooltip.html(html)
                .style("left", (event.pageX + 10) + "px")
                .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
            .duration(300)
            .style("opacity", 0.8)
            .style("visibility", "visible");;
            
        })
        .on("mouseout", d => {
            this.tooltip.transition()
                .duration(300)
                .style("opacity", 0)
                .style("visibility", "hidden");
        });
    }

    updateData(data) {
        this.chart.selectAll("circle").remove();
        var container = this.getContainer();
        var div_id = container.replace("#", "");
        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", 2.4)
            .attr("cx", d => { return this.xScale(d.x); })
            .attr("cy", d => { return this.yScale(d.y); })
            .attr("id", function (d) { return "plot-" + div_id + "-" + d.key; })
            .style("fill", d => { return d.color })
            .style("cursor", "pointer")
            .on("mouseover", (event, d) => {
                console.log(d);
                // var region = getRegion([d.x, d.y], duvT1Zones);
                var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
                html += '<tr><td>Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td>C2H6 </td><td>' + d.c2h6.toFixed(2) + '('+ d.c2h6_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td>H2 </td><td>' + d.h2.toFixed(2) + '('+ d.h2_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td>C2H4 </td><td>' + d.c2h4.toFixed(2) + '('+ d.c2h4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td>C2H2 </td><td>' + d.c2h2.toFixed(2) + '('+ d.c2h2_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td>CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td>Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';
                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                    this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");;
                
            })
            .on("mouseout", d => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0)
                    .style("visibility", "hidden");
            });

        this.d3.select("#plot-" + div_id + "-" + (chart_data.length - 1)).transition().duration(100).attr('r', 4);
    }
}