<template>
    <div id="triangle7">
    </div>
</template>

<script>
    import { Triangle7 } from '@/components/configs/duvalst7.js'
    export default {
        name : 'DuvalsTriangleSeven',
        props : ['data'],
        data() {
            return {
                duvals : null
            }
        },
        watch: {
            'data'() {
                this.update()
            }
        },
        mounted() {
            let data = this.data.map(d => {
                d.interpretation = d.t7;
                return d;
            });

            this.duvals = new Triangle7('triangle7');
            this.duvals.load(data);
        },
        methods : {
            update() {
                let data = this.data.map(d => {
                    d.interpretation = d.t7;
                    return d;
                });

                this.duvals.updateData(data);
            }
        }
    }
</script>