<template>
    <v-dialog v-model="dialog" width="300" @click:outside="closeDialog()">
        <div class="dialog-body">
            <v-alert
                color="green "
                >
                <div>Normal</div>
                <div>
                    <h4>{{ data.normal.min }} - {{ data.normal.max }}</h4>
                </div>
            </v-alert>
            <v-alert
                color="yellow darken-1"
                >
                <div>Level 1 Warning</div>
                <div>
                    <h4>{{ data.medium.min }} - {{ data.medium.max }}</h4>
                </div>
            </v-alert>
            <v-alert v-if="data.critical.min && data.critical.max != 1"
                color="orange "
                >
                <div>Level 2 Warning</div>
                <div>
                    <h4>{{ data.critical.min }} - {{ data.critical.max }}</h4>
                </div>
            </v-alert>
            <v-alert
                color="red"
                dark
                class="mb-0"
                >
                <div>Pre-Failure : </div>
                <div>
                    <h4>{{ data.failure.min }} - {{ data.failure.max }}</h4>
                </div>
            </v-alert>
        </div>
    </v-dialog>
</template>
<style scoped>
.dialog-body {
    background : #e3e3e3;
    padding : 10px;
}
</style>
<script>

export default {
    name : 'GasLimitDetails',
    props : ['dialog', 'data', 'details'],
    methods: {
        closeDialog() {
            this.$emit('close');
        }
    }
}
</script>