<template>
    <div>
      <JSCharting  style="height:100%;" :options="options"></JSCharting>
    </div>
  </template>
  
  <script>
import JSCharting, {JSC} from 'jscharting-vue';
import { gassesMixin } from '@/mixins/gassesMixin.js';
  export default {
     name: 'LineGraph',
     props : ['attribute_key','data'],
     mixins : [gassesMixin],
     data() {

        let options = {
            debug: false,
            box_fill: "transparent",
            defaultPoint: {
                focusGlow: false,
            },
            xAxis: { scale: { type: 'time' } },
            xAxis_overflow: 'hidden',
            yAxis_overflow: 'show',
            legend: {
                position: 'inside top right',
                style_fontSize: 12,
                margin_top: -25,
                fill:"#BDBDBD",
            },
            series: [],
            defaultSeries: {
                type: 'Spline',
                line_width: 2.5,
                opacity: 1,
                defaultPoint_marker_size: 10
            },
            title_label: {
                text: '<b>Gas Trend</b>',
                color: 'red',
                style_fontSize: 12
            },
        };
        return { options };
    },
    watch :  {
        'data' () {
            this.options.series = [
                {
                    name : this.attribute_key,
                    points : []
                }
            ]
            this.data.forEach(d => {
                this.options.series[0].points.push(
                    [d.timestamp, parseFloat(d[this.attribute_key])]
                )
            });

            console.log(this.data.attribute_key);
            console.log(this.options);
        }
    },

    components: {
       JSCharting
    }
  };
  </script>

  