<template>
  <v-row justify="center">
    <v-dialog v-model="show" persistent max-width="600" @click:outside="closeLogin()">
      <v-card>
        <v-img height="100" src="@/assets/login-bg.jpg"></v-img>
        <v-divider></v-divider>
        <v-form pt="12">
          <v-container>
            <v-row>
              <v-col col="12" md="5" class="">
                <div class="text-center mt-5 mb-2 mt-10">
                  <img v-if="!$vuetify.theme.dark" src="@/assets/logo-black.png" alt="" width="140" class="login-logo">
                  <img v-if="$vuetify.theme.dark" src="@/assets/logo.png" alt="" width="140">
                  <div class="mt-3">
                    <small>Transformer Monitoring Analytics</small>
                  </div>
                </div>
                <!-- <v-list-item class="">

                  <v-list-item-content class="custom-content text-center">
                    <v-list-item-subtitle>
                      Dissolved Gas Analysis
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item> -->

              </v-col>
              <v-divider vertical></v-divider>
              <v-col col="12" md="7">
                <v-list-item-content class="custom-content text-center">
                  <v-list-item-title class="text-h6 pt-5">
                    Login or Sign Up
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    Using
                  </v-list-item-subtitle>
                </v-list-item-content>
                <v-card-text>
                  <div class="my-2">
                    <v-btn block dark color="red" class="darken-2" @click="signInWithGoogle()">
                      <v-icon left>mdi-google</v-icon> Google
                    </v-btn>
                  </div>
                  <div class="my-2">
                    <v-btn block dark color="blue" class="darken-3" @click="signInWithMicrosoft()">
                      <v-icon left>mdi-microsoft</v-icon> Microsoft
                    </v-btn>
                  </div>
                </v-card-text>
              </v-col>

            </v-row>
          </v-container>
        </v-form>
       
      </v-card>
    </v-dialog>
  </v-row>
</template>
<style scoped>
  .login-logo {
    transition: 1s ease-in-out;
  }
  .login-logo.rotate {
    transform : rotate(360deg);
  }
</style>
<script>
import { msalInstance } from "vue-msal-browser"
import axios from 'axios'

export default {
  props: ['show'],
  data(){
    return {
      rotate : false
    }
  },
  watch: {
    'show'() {
      setTimeout(f => {
        this.rotate = this.show ? true : false;
      }, 300)
      
    }
  },
  methods: {
    closeLogin() {
      this.$emit('update:login_dialog', false)
    },
    async signInWithGoogle() {
      // const googleUser = await this.$gAuth.signIn();
      // console.log(googleUser);

      // 

      this.$gAuth.signIn().then(user => {
        this.$emit('authenticate', 'google', user);
      })
    },

    async signInWithMicrosoft() {
      let graphResponse = await msalInstance.authenticatePopup();

      this.$emit('authenticate', 'ms', graphResponse);
    }
  }
}
</script>