import {Duvals } from './duvals';
export class Triangle1 extends Duvals{
	load(data) {
        this.d3 = this.getD3();
        this.config = this.getConfig();
        var color = {
            PD : 'pink',
            T1 : 'purple',
            T2 : 'green',
            T3 : 'HotPink',
            DT : 'blue',
            D1 : 'SkyBlue',
            D2 : '#679A00',
        };
        var points = [
            {"x":0, "y":57.73502692}, //1
            {"x":-1, "y":56.003}, //2
            {"x":1, "y":56.003}, //3
            {"x":-2, "y":54.271}, //4
            {"x":8, "y":36.95}, //5
            {"x":10, "y":40.415}, //6
            {"x":23, "y":10.96965511}, //7
            {"x":25, "y":14.43375673}, //8
            {"x":17.5, "y":1.443375673}, //9
            {"x":35, "y":-28.86751346}, //10
            {"x":50, "y":-28.86751346}, //11
            {"x":-6.5, "y":46.47669667}, //12
            {"x":13.5, "y":11.83568052}, //13
            {"x":5.5, "y":-2.020725942}, //14
            {"x":21, "y":-28.86751346}, //15
            {"x":5, "y":26.55811238}, //16
            {"x":-27, "y":-28.86751346}, //17
            {"x":-50, "y":-28.86751346} //18            
               
         ];

        var orders = [
            {"name":"PD", "order":[1, 2, 3]},
            {"name":"T1", "order":[3, 2, 4, 5, 6]},
            {"name":"T2", "order":[6, 5, 7, 8]},
            {"name":"T3", "order":[8, 9, 10, 11]},
            {"name":"DT", "order":[4, 12, 13, 14, 15, 10, 9, 7]},
            {"name":"D1", "order":[12, 18, 17, 16]},
            {"name":"D2", "order":[16, 17, 15, 14, 13]}
        ];

        var zones = [];

        orders.forEach(function (o) {
            var polygon_pts = [];
            o.order.forEach(function (z) {
                polygon_pts.push({"x" : points[z - 1].x, "y" : points[z - 1].y});
            });

            zones.push({name : o.name, points : polygon_pts});
        });


        var margin = {top: 0, right: 0, bottom: 0, left: 0};
        var width = this.config.width - margin.left - margin.right;
        var height = this.config.height - margin.top - margin.bottom;


        var svg = this.d3.select('#' + this.container).append("svg")
            .attr("preserveAspectRatio", "xMinYMin meet")
            .attr("viewBox", "0 0 438.8 380")
        this.chart = svg.append("g").attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        this.xScale = this.d3.scaleLinear().range([0,width]);
        this.yScale = this.d3.scaleLinear().range([height,0]);

        this.xScale
            .domain([-60, 60])
            .nice();
        this.yScale
            .domain([-35, 70])
            .nice();
        this.chart.selectAll("polygon2")
            .data(zones)
            .enter().append("polygon")
            .attr("points", d => { return d.points.map(d => { 
                    return [this.xScale(d.x), this.yScale(d.y)].join(","); 
                }).join(" ");})
            .attr("fill", d =>{
                return color[d.name];
            })
        //.attr("stroke","#666")
            .attr("stroke-width",2);

        svg.append("svg:text")
            .attr("x", 320)
            .attr("y", -190)
            .attr("transform", "rotate(60)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% C2H4")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("stroke", "black")
            .style("font-family", "calibri");

        svg.append("svg:text")
            .attr("x", 215)
            .attr("y", 365)
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "11px")
            .text("% C2H2")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");

        svg.append("svg:text")
            .attr("x", -105)
            .attr("y", 191)
            .attr("transform", "rotate(-60)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% CH4")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");

        // svg.append("svg:text")
        //     .attr("x", 380)
        //     .attr("y", 360)
        //     .attr("text-anchor", "middle")
        //     .style("font", "sans-serif")
        //     .style("font-size", "8px")
        //     .text("Powered By Calibr8 Systems Inc.")
        //     .style("fill", "#333")
        //     .style("stroke-width", "0px")
        //     .style("font-family", "calibri");


        this.tooltip = this.d3.select('body')
            .append('div')
            .attr('id', this.container + '-tooltip')
            .style('padding', '5px')
            .style('font-size', '10px')
            .style('background', '#fff')
            .style('opacity', 0)
            .style('position', 'absolute')
            .style('visibility', 'hidden')
            .style('font-family', 'calibri')
            .style('border', '1px solid #cccccc');

        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", 2.5)
            .attr("cx", d => { return this.xScale(d.x); })
            .attr("cy", d => { return this.yScale(d.y); })
            .attr("id", d => { return "plot-" + this.container + "-" + d.key; })
            .style("opacity", 0.2)
            .style("fill", d => { d.color; })
            .on("mouseover", (event, d) => {
                var html = '<table width="100%" class="basic-table" style="font-size:11px;">';
                html += '<tr><td style="font-weight:bold;">Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H2 </td><td>' + d.c2h2.toFixed(2) + '('+ d.c2h2_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H4 </td><td>' + d.c2h4.toFixed(2) + '('+ d.c2h4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';
                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");
            })
            .on("mouseout", d => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0)
                    .style("visibility", "hidden");
            });

        var duration = 1000 / chart_data.length;
        chart_data.forEach((val, idx) => {
            var plot = this.d3.select('#plot-' + this.container + '-' + idx).transition()
                .duration((idx + 1) * duration)
                .style('fill', val.color)
                .style("opacity", 1)
                .style("cursor", "pointer");

                if(idx == chart_data.length - 1) {
                    plot.attr('r', 4);
                }
        });
	}

	getChartData(data) {
		var chart_data = [];
	    data.forEach((d, idx) => {
	        var c2h4 = d.c2h4;
	        var c2h2 = d.c2h2;
	        var ch4 = d.ch4;
	        var timestamp = d.timestamp;
	        var interpretation = d.interpretation;

	        var tr_l = 100; //Equilateral triangle side length = 100, Origin at 33.33% of length of each side
	        var tr_h = Math.sqrt(Math.pow(tr_l, 2) - Math.pow(tr_l / 2, 2)); //86.60254038
	        var tr_ymax = (tr_l / 2) / Math.cos((30/180) * Math.PI); //57.73502692
	        var tr_ymin = tr_ymax - tr_h;  //-28.86751346


	        var dt1_total = c2h4 + c2h2 + ch4; //Getting the percentage of each value
	        var c2h4_pc = (c2h4 / dt1_total) * 100;
	        var c2h2_pc = (c2h2 / dt1_total) * 100;
	        var ch4_pc = (ch4 / dt1_total) * 100;

	        var dt1_x = (c2h4_pc - c2h2_pc) * Math.sin(30 * Math.PI / 180);
	        var dt1_y = (ch4_pc / 100) * tr_h + tr_ymin;
	        
	        var chart_data_obj = {
	            key : idx,
	            x : dt1_x,
	            y : dt1_y,
	            c2h4 : c2h4,
	            c2h2 : c2h2,
	            ch4 : ch4,
	            c2h4_pc : c2h4_pc,
	            c2h2_pc : c2h2_pc,
	            ch4_pc : ch4_pc,
	            date : timestamp,
	            interpretation : interpretation,
	            color : this.getHexColor(idx, data.length),
                radius : (idx < data.length - 1 ? 3 : 5)
	        };
            if (interpretation !== 'Normal') {
                chart_data.push(chart_data_obj);
            }
	        // chart_data.push(chart_data_obj);
	    });

	    return chart_data;

        
	}
    

	updateData(data) {
        var div_id = this.container.replace('#', '');
		this.chart.selectAll("circle").remove();
        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", d => { return d.radius; })
            .attr("cx", d => { return this.xScale(d.x); })
            .attr("cy", d => { return this.yScale(d.y); })
            .attr("id", d => { return "plot-" + this.container + "-" + d.key; })
            .style("fill", d => { return d.color })
            .style("cursor", "pointer")
            .on("mouseover", (event, d) => {
                var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
                html += '<tr><td style="font-weight:bold;">Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H2 </td><td>' + d.c2h2.toFixed(2) + '('+ d.c2h2_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H4 </td><td>' + d.c2h4.toFixed(2) + '('+ d.c2h4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';
                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");
            })
            .on("mouseout", d => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0)
                    .style("visibility", "hidden");
            });

            this.d3.select("#plot-" + div_id + "-" + (chart_data.length - 1)).transition().duration(100).attr('r', 4);
	}
}