import {Duvals } from './duvals';
export class Triangle4 extends Duvals{
	load(data) {
        this.d3 = this.getD3();
		var config = this.getConfig();
        var color = {
            PD : 'pink',
            S : 'purple',
            C : 'LimeGreen',
            O : 'HotPink',
            ND : 'blue',
        };
        
        var points = [
            {"x":1, "y":56.00297611}, //1
            {"x":0.5, "y":55.13695071}, //2
            {"x":7, "y":43.87862046}, //3
            {"x":7.5, "y":44.74464586}, //4
            {"x":18, "y":26.55811238}, //5
            {"x":6, "y":5.773502692}, //6
            {"x":18.5, "y":-15.8771324}, //7
            {"x":12.5, "y":-15.8771324}, //8
            {"x":20, "y":-28.86751346}, //9
            {"x":50, "y":-28.86751346}, //10
            {"x":0, "y":57.73502692}, //11
            {"x":-23, "y":17.89785834}, //12
            {"x":0.5, "y":-21.07328483}, //13
            {"x":15.5, "y":-21.07328483}, //14
            {"x":-45.5, "y":-21.07328483}, //15
            {"x":-50, "y":-28.86751346}, //15        
               
         ];

        var orders = [
            {"name":"PD", "order":[1, 2, 3, 4]},
            {"name":"S", "order":[11, 12, 13, 14, 8, 7, 6, 5, 4, 3, 2, 1]},
            {"name":"C", "order":[5, 6, 7, 8, 9, 10]},
            {"name":"O", "order":[14, 15, 16, 9]},
            {"name":"ND", "order":[12, 15, 13]}
        ];

        var zones = [];

        orders.forEach(function (o) {
            var polygon_pts = [];
            o.order.forEach(function (z) {
                polygon_pts.push({"x" : points[z - 1].x, "y" : points[z - 1].y});
            });

            zones.push({name : o.name, points : polygon_pts});
        });


        var margin = {top: 0, right: 0, bottom: 0, left: 0};
        var width = config.width - margin.left - margin.right;
        var height = config.height - margin.top - margin.bottom;

        var self = this;

        var svg = this.d3.select('#' + this.container).append("svg")
            .attr("preserveAspectRatio", "xMinYMin meet")
            .attr("viewBox", "0 0 438.8 380")
        this.chart = svg.append("g").attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        this.xScale = this.d3.scaleLinear().range([0,width]);
        this.yScale = this.d3.scaleLinear().range([height,0]);

        this.xScale
            .domain([-60, 60])
            .nice();
        this.yScale
            .domain([-35, 70])
            .nice();
        this.chart.selectAll("polygon2")
            .data(zones)
            .enter().append("polygon")
            .attr("points",function(d) { return d.points.map(function(d) { 
                    return [self.xScale(d.x), self.yScale(d.y)].join(","); 
                }).join(" ");})
            .attr("fill", function(d){
                return color[d.name];
            })
        //.attr("stroke","#666")
            .attr("stroke-width",2);

            svg.append("svg:text")
            .attr("x", 320)
            .attr("y", -190)
            .attr("transform", "rotate(60)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% C2H4")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");

        svg.append("svg:text")
            .attr("x", 215)
            .attr("y", 365)
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% C2H2")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");

        svg.append("svg:text")
            .attr("x", -105)
            .attr("y", 191)
            .attr("transform", "rotate(-60)")
            .attr("text-anchor", "middle")
            .style("font", "sans-serif")
            .style("font-size", "10px")
            .text("% CH4")
            .style("fill", "green")
            .style("stroke-width", "0px")
            .style("font-family", "calibri");


        // svg.append("svg:text")
        //     .attr("x", 380)
        //     .attr("y", 360)
        //     .attr("text-anchor", "middle")
        //     .style("font", "sans-serif")
        //     .style("font-size", "8px")
        //     .text("Powered By Calibr8 Systems Inc.")
        //     .style("fill", "#333")
        //     .style("stroke-width", "0px")
        //     .style("font-family", "calibri");


        this.tooltip = this.d3.select('body')
            .append('div')
            .attr('id', this.container + '-tooltip')
            .style('padding', '3px')
            .style('font-size', '10px')
            .style('background', '#fff')
            .style('opacity', 0)
            .style('position', 'absolute')
            .style('visibility', 'hidden')
            .style('font-family', 'calibri')
            .style('border', '1px solid #cccccc');

        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", d => { return d.radius; })
            .attr("cx", d => { return self.xScale(d.x); })
            .attr("cy", d => { return self.yScale(d.y); })
            .attr("id", d => { return "plot-" + self.container + "-" + d.key; })
            .style("opacity", 0.2)
            .style("fill", d => { d.color; })
            .on("mouseover", (event, d) => {
                var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
                html += '<tr><td style="font-weight:bold;">Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H6 </td><td>' + d.c2h6.toFixed(2) + '('+ d.c2h6_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">H2 </td><td>' + d.h2.toFixed(2) + '('+ d.h2_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';
                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");
            })
            .on("mouseout", d => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0.8)
                    .style("visibility", "hidden");
            });

        var duration = 1000 / chart_data.length;
        chart_data.forEach((val, idx) => {
            var plot = this.d3.select('#plot-' + self.container + '-' + idx).transition()
                .duration((idx + 1) * duration)
                .style('fill', val.color)
                .style("opacity", 1)
                .style("cursor", "pointer");
        });
	}

	getChartData(data_arr) {
		var chart_data = [];
        data_arr.forEach((data, idx) => {
            var ch4 = data.ch4;
            var c2h6 = data.c2h6;
            var h2 = data.h2;
            var timestamp = data.timestamp;
            var interpretation = data.interpretation;

            var tr_l = 100; //Equilateral triangle side length = 100, Origin at 33.33% of length of each side
            var tr_h = Math.sqrt(Math.pow(tr_l, 2) - Math.pow(tr_l / 2, 2)); //86.60254038
            var tr_ymax = (tr_l / 2) / Math.cos((30/180) * Math.PI); //57.73502692
            var tr_ymin = tr_ymax - tr_h;  //-28.86751346


            var total = ch4 + c2h6 + h2; //Getting the percentage of each value
            var ch4_pc = (ch4 / total) * 100;
            var c2h6_pc = (c2h6 / total) * 100;
            var h2_pc = (h2 / total) * 100;

            var dt_x = (ch4_pc - c2h6_pc) * Math.sin(30 * Math.PI / 180);
            var dt_y = (h2_pc / 100) * tr_h + tr_ymin;
            
            var chart_data_obj = {
                key : idx,
                x : dt_x,
                y : dt_y,
                ch4 : ch4,
                c2h6 : c2h6,
                h2 : h2,
                ch4_pc : ch4_pc,
                c2h6_pc : c2h6_pc,
                h2_pc : h2_pc,
                date : timestamp,
                interpretation : interpretation,
                color : this.getHexColor(idx, data_arr.length),
                radius : (idx < data_arr.length - 1 ? 2.5 : 4)
            };

            if (interpretation !== 'Normal') {
                chart_data.push(chart_data_obj);
            }
            // chart_data.push(chart_data_obj);
        });

        return chart_data;
	}

	updateData(data) {
		var div_id = this.container.replace("#", "");
		this.chart.selectAll("circle").remove();
        var chart_data = this.getChartData(data);
        this.chart.selectAll("points")
            .data(chart_data)
            .enter().append("circle")
            .attr("r", 2.4)
            .attr("cx", d => { return this.xScale(d.x); })
            .attr("cy", d => { return this.yScale(d.y); })
            .attr("id", d => { return "plot-" + this.container + "-" + d.key; })
            .style("fill", d => { return d.color })
            .style("cursor", "pointer")
            .on("mouseover", (event, d) => {
                var html = '<table width="100%" class="basic-table" style="font-size:12px;">';
                html += '<tr><td style="font-weight:bold;">Timestamp </td><td>' + d.date + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">CH4 </td><td>' + d.ch4.toFixed(2) + '('+ d.ch4_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">C2H6 </td><td>' + d.c2h6.toFixed(2) + '('+ d.c2h6_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">H2 </td><td>' + d.h2.toFixed(2) + '('+ d.h2_pc.toFixed(2) +'%)' + '</td></tr>';
                html += '<tr><td style="font-weight:bold;">Fault </td><td>' + d.interpretation + '</td></tr>';
                html += '</table>';

                this.tooltip.html(html)
                    .style("left", (event.pageX + 10) + "px")
                    .style("top", (event.pageY - 10) + "px")
                this.tooltip.transition()
                .duration(300)
                .style("opacity", 0.8)
                .style("visibility", "visible");
            })
            .on("mouseout", (event, d) => {
                this.tooltip.transition()
                    .duration(300)
                    .style("opacity", 0)
                    .style("visibility", "hidden");
            });

            this.d3.select("#plot-" + div_id + "-" + (chart_data.length - 1)).transition().duration(100).attr('r', 4);
	}
}