<template>
    <v-dialog 
        v-model="dialog" 
        persistent
        max-width="290"
        @click:outside="close()">
        <v-time-picker
            v-model="time"
            color="indigo darken-3"
            header-color="indigo"
            >
            <v-spacer></v-spacer>
            <v-btn
                small
                color="primary"
                @click="setTime()"
            >
                Confirm
            </v-btn>
        </v-time-picker>
    </v-dialog>
</template>
<script>
export default {
    name : 'TimePicker',
    props : ['dialog', 'current_time'],
    watch : {
        'current_time' () {
            this.time = this.current_time;
        }
    },
    data() {
        return {
            time : null
        }
    },
    methods : {
        setTimeNow() {
            var date = new Date(); 
            var hour = date.getHours() <= 9 ? '0' + date.getHours() : date.getHours();
            var mins = date.getMinutes() <= 9 ? '0' + date.getMinutes() : date.getMinutes();
            var time = hour + ':' + mins;
            
            this.$emit('setTime', time);
        },
        setTime() {
            this.$emit('setTime', this.time);
        },
        close() {
            this.$emit('close');
        }
    }
}

</script>